import React, { useEffect, useState } from 'react';
import { Link ,useParams} from 'react-router-dom';
import Select from 'react-select';
import TextInput2 from '../../components/formComponents/TextInput2';
import { useForm } from 'react-hook-form';
import { client } from '../../Constant';
import Swal from 'sweetalert2';
import Loading from '../../assets/images/loading.gif';
import { responseHandler } from '../../components/api/logics';




const EditUser = () => {
    // State to store user data
    const [userData, setUserData] = useState([]);
    const [newPassword,setNewPassword] =useState('');
    // Get user ID from route parameters
    const { id } = useParams();
    const userId = +id;

    // Form handling using react-hook-form
    const { register, handleSubmit, control } = useForm();
    const [formValues, setFormValues] = useState({
        userType: null,
        fullName: '',
        sapNum: '',
        department: '',
        email: '',
        phone: '',
    });
    const [user_type, setUserType] = useState({
        value: '',
        label: ''
    });
    

    // Fetch user data from the API when the component mounts
    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await getUser(userId);
                setUserData(response);
                // console.log(response);
            } catch (error) {
                console.log(error);
            }
        };

        fetchData();
    }, [userId]); // Depend on userId to refetch data when userId changes

    // Function to get user data from the API
    const getUser = async (id) => {
        try {
            const response = await client.get(`/users/${id}`);
            return response.data;
        } catch (error) {
            console.log(error);
            Swal.fire({
                title: error.message,
                icon: 'error',
                confirmButtonText: 'OK'
            });
            return [];
        }
    };

    // User types for userType select
    const userType = [
        { value: 'SuperAdmin', label: "Super Admin" },
        { value: 'Admin', label: "Admin" },
        { value: 'Engineer', label: "Engineer" },
        { value: 'Technician', label: "Technician" },
        { value: 'Painter', label: "Painter" },
        { value: 'User', label: "User" },
    ];

    // Handle change in userType select
    const onChange = async (data) => {
        const privileges = data;
        const formData = [];
        formData.push({
            userInfo:{
                name:formValues.fullName,
                email:formValues.email,
                contact:formValues.phone,
                sap:formValues.sapNum,
                department:formValues.department,
                userType:user_type.value
            },

            privileges:data
        });
        
        const response = await updateUser(userId,formData);

        responseHandler(response)
        //console.log(formData);
        
    };

    // Set selected value for user type when user data changes
    useEffect(() => {
        const selectedUserType = userType.find((type) => type.value === userData[0]?.user_type);
        setUserType(selectedUserType);
    }, [userData]); // Depend on userData to update when userData changes

    // Update form values when user data changes
    useEffect(() => {
        if (Array.isArray(userData) && userData.length > 0) {
            setFormValues({
                ...formValues,
                fullName: userData[0]?.name || '',
                sapNum: userData[0]?.sap || '',
                department: userData[0]?.departments || '',
                email: userData[0]?.email || '',
                phone: userData[0]?.tel_no || ''
            });
        }
    }, [userData]); // Depend on userData to update formValues when userData changes


    //update user
    const updateUser = async (id, data) => {
        const formData = new FormData();
        
        if(data && data.length>0){
            // Append user information
                formData.append('uid', id);
                formData.append('name', data[0].userInfo.name);
                formData.append('email', data[0].userInfo.email);
                formData.append('sap', data[0].userInfo.sap);
                formData.append('tel_no', data[0].userInfo.contact);
                formData.append('departments', data[0].userInfo.department);
                formData.append('user_type', data[0].userInfo.userType);
                
                // // Append privileges data
                Object.entries(data[0].privileges).forEach(([key, value]) => {
                    // Skip 'file' property
                    if (key === 'file') {
                        return;
                    }
            
                    // If the value is '1' (string) or true (boolean), append to formData
                    if (value === '1' || value === true) {
                        formData.append(key, '1');
                    }else{
                        formData.append(key, '');
                    }

                });
            
                // // Append avatar (file) if available
                if ( data[0].privileges.file[0]) {
                    formData.append('avatar', data[0].privileges.file[0]);
                }
                try {
                    const response = await client.put(`/users/${id}`, formData, {
                        headers: {
                            'Content-Type': 'multipart/form-data',
                        },
                    });
                    console.log(response)
                    return response.data;
                } catch (error) {
                    console.log(error)
                    Swal.fire({
                        icon: "error",
                        title: "Oops...",
                        text: error.message,
                        
                    });
                    return {error:error.message}
                }
                
            
            

        }
        
        
    };
    

    /// change password
    const changePassword = async()=>{

        if(newPassword.trim()===''){
            Swal.fire({
                icon:"warning",
                title:'Error',
                titleText:"Field should not be empty",
                confirmButtonText:'OK'
            });
            return null;
        }
        Swal.fire({
            title: "Do you want to change new password?",
            icon:'question',
            showCancelButton: true,
            confirmButtonText: "Yes",
           
          }).then(async(result) => {
            /* Read more about isConfirmed, isDenied below */
            if (result.isConfirmed) {
                try {
                    const response = await client.put('/users/change-password',{
                        id:userId,
                        password:newPassword
                    });
                    responseHandler(response.data);
                } catch (error) {
                    const response= {error:error.message}
                    responseHandler(response)
                }
            } 
          });
        
    }


    // Render loading spinner if user data is not available
    if (!userData || userData.length === 0) {
        return (
            <div className='text-center'>
                <img src={Loading} alt="Loading..." width={70} height={70} />
            </div>
        );
    }
  return (
    <div className='edit-user'>
        <div className="page-title-box d-flex justify-content-between">
            <h4 className="page-title">Edit User</h4>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb">
                    <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
                    <li className="breadcrumb-item"><a href="#">User</a></li>
                    <li className="breadcrumb-item active" aria-current="page">Edit User</li>
                </ol>
            </nav>
        </div>
        <div className="content">
            <form className='col' onSubmit={handleSubmit(onChange)}>
                <div className="row">
                    <div className="col-6">
                        <div className="row">
                            <label htmlFor="" className='col-3 fw-bold'>User Type:</label>
                            <Select 
                            className='col p-0' 
                            options={userType} 
                            value={user_type}
                            onChange={(selectedOption)=>setUserType(selectedOption)}
                            />
                        </div>
                    </div>
                    <TextInput2
                    label={'Name in Full'}
                    type={'text'}
                    onchange={(e)=>{setFormValues({...formValues, fullName:e.target.value})}}
                    value={formValues.fullName}
                    
                    />
                </div>
                <div className="row mt-4">
                    <TextInput2
                    label={'SAP Number'}
                    type={'text'}
                    onchange={(e)=>{setFormValues({...formValues, sapNum:e.target.value})}}
                    value={formValues.sapNum}
                    />
                    <div className="col-6">
                        <div className="row">
                            <label htmlFor="" className='col-3 fw-bold'>Department:</label>
                            <Select className='col p-0' />
                        </div>
                    </div>
                </div>
                <div className="row mt-4">
                    <TextInput2 
                        label={'Email'}
                        type={'email'}
                        onchange={(e)=>{setFormValues({...formValues, email:e.target.value})}}
                        value={formValues.email}
                    />
                    <TextInput2 
                        label={'Phone'}
                        type={'tel'}
                        onchange={(e)=>{setFormValues({...formValues, phone:e.target.value})}}
                        value={formValues.phone}
                    />
                </div>
                <div className="row mt-4">
                    <div className="col-6">
                        <div className="row">
                            <label htmlFor="" className='col-3 fw-bold'>Picture:</label>
                            <input className="form-control col" type="file" id="formFileMultiple" multiple 
                            {...register('file')}
                            />
                        </div>
                    </div>
                </div>



                <div className="col-md-12 mt-5"> 
                    <div className="row">
                        <div className="col-md-12"><h4 className="permission_u">Permissions</h4></div>									
                        <div className="col-md-3 mt-2">
                            <h6>Manage jobs</h6>
                            <input type="checkbox"  className="pri_cbox" value="1" checked {...register("job_add")}/> <label>Add<span></span></label> <br />
                            <label><input type="checkbox"   className="pri_cbox" value="1"{...register("job_edit")}  /> Edit<span></span></label> <br />
                            <label><input type="checkbox"   className="pri_cbox" value="1" checked {...register("job_view")}/> View<span></span></label> <br />
                        </div>
                        <div className="col-md-3 mt-2">
                            <h6>Departments</h6>
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("dep_add")}/> Add<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("dep_edit")}/> Edit<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("dep_view")}/> View<span></span></label> <br />
                        </div>
                        <div className="col-md-3 mt-2">
                            <h6>Location manager</h6>
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("loc_add")}/> Add<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("loc_edit")}/> Edit<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("loc_view")}/> View<span></span></label> <br />
                        </div>
                        <div className="col-md-3 mt-2">
                            <h6>Monthly Roater</h6>
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("rost_add")}/> Add<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox"  value="1" {...register("rost_edit")}/> Edit<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("rost_view")}/> View<span></span></label> <br />
                        </div>
                        <div className="col-md-3 mt-2">										
                            <h6>Equipments</h6>
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("cat_add")}/> Add category<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("cat_edit")}/> Edit Category<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("cat_view")}/> View Category<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("eqp_add")}/> Add Equipments<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("eqp_edit")}/> Edit Equipments<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("eqp_view")}/> View Equipments<span></span></label> <br />
                        </div>
                        <div className="col-md-3 mt-2">
                            <h6>User management</h6>
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("super_up")}/> Super Admin user Creation/Edit<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("admin_up")}/> Admin user creation /Edit<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("tech_up")}/> Technician Creation/edit<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("user_up")}/> Other Users creation/edit<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("user_view")}/> View Users<span></span></label> <br />
                        </div>
                        <div className="col-md-3 mt-2">
                            <h6>Meter Readings</h6>
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("meter_add")}/> Add<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("meter_edit")}/> Edit<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("meter_view")}/> View<span></span></label> <br />
                        </div>
                        
                        
                        <div className="col-md-3 mt-2">										
                            <h6>Reports</h6>
                            <label><input type="checkbox"  className="pri_cbox" value="1"{...register("pending_job")} /> Pending jobs<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("complete_job")}/> Completed jobs<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("cancel_job")}/> Canceled jobs<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("total_job")}/> Total received jobs<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("jobcomplete_technician")}/> Jobs completed by a technician<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("floorwise_job")}/> Sort out the jobs floor wise<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("departwise_job")}/> Sort out the jobs department wise<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("holdpm_work")}/> Hold PM works<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("completepm_work")}/> Completed PM work with Equipments<span></span></label> <br />
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("upcomingpm_work")}/> Upcoming PM work<span></span></label> <br />
                        </div>
                        
                        <div className="col-md-3 mt-2">
                            <h6>Settings</h6>
                            <label><input type="checkbox"  className="pri_cbox" value="1" {...register("setting")}/> Update<span></span></label> <br />
                        </div>

                    </div>
                </div>
                <div className="mt-5">
                    <button type="submit" className='btn btn-primary'>Update</button>
                </div>

            </form>
            <div className="col-6 mt-5 mb-5">
                <h4>Change Password</h4>
                <div className="d-flex align-items-baseline mt-3">
                    <label className='col-3 fw-bold' htmlFor="change-password">New Password:</label>
                    <input type="password" id="" 
                    className='form-control col-6' 
                    onChange={(e)=>{setNewPassword(e.target.value)}}
                    />
                    <button className="btn btn-primary col-3 ms-1" onClick={changePassword}>Change Password</button>
                </div>
            </div>

        </div>
    </div>
  )
}

export default EditUser
