import React, { useEffect, useState } from 'react'
import user_profile from '../../../assets/images/user-profile.png';
import { Link } from 'react-router-dom';
import { client } from '../../../Constant';
import Loading from '../../../assets/images/loading.gif';
import Swal from 'sweetalert2';
import { responseHandler } from '../../../components/api/logics';


const PersonalDetails = ({userProfile}) => {
    //console.log(userProfile)
    const [avatar,setAvatar] = useState(user_profile)
    const user = userProfile[0];
    useEffect(() => {
        const fetchImage = async () => {
          if (user && user.picture) {
            try {
              const response = await client.get(`/user-image/${user.picture}`);
              // Assuming response.data contains the image data
              if(response.data !='')
                setAvatar(response.data);
            } catch (error) {
              console.error("Error fetching image:", error);
              // Handle the error, e.g., set a default image or show an error message
            }
          }
        };
        fetchImage();
      }, [user]);

      //deactivate profile
      const deactivate =async (id,) => {

        
        //call api
        Swal.fire({
          title: `Do you want to DEACTIVATE this profile?`,
          icon:'question',
          showCancelButton: true,
          confirmButtonText: "Yes",
         
        }).then(async(result) => {
          /* Read more about isConfirmed, isDenied below */
          if (result.isConfirmed) {
              try {
                  const response = await client.put(`/users/change-status/:${user.id}`,{
                      id:user.id,
                      status:'inactive'
                  });
                  responseHandler(response.data);
              } catch (error) {
                  const response= {error:error.message}
                  responseHandler(response)
              }
              window.location.href='/view-users';
          } 
        });
        
      };
      
    if (!user) {
        // Handle the case when user data is not yet available
        return <div>
          <img src={Loading} alt="Loading..." width={70} height={70} />
        </div>;
    }
    
    
  return (
    <div className=" card a-border">
                <div className="card-body">
                    <div className="text-center mt-2">
                        <img 
                        src={avatar} 
                        alt={user.name } 
                        width='72px' 
                        height='72px' 
                        className='rounded-circle'
                        />
                        <h4 className="mt-2 mb-0">{user.name}</h4>
                        <h6 className="text-muted fw-normal mt-2 mb-0">
                            {user.user_type}
                        </h6>
                        <h6 className="text-muted fw-normal mt-1 mb-3">
                            {user.departments}
                        </h6>
                    </div>
                    <div className="mt-3 pt-2 border-top">
                        <h4 className="mb-2 fs-15">Contact Information</h4>
                        <div className="table-responsive">
                            <table className="table table-borderless mb-0 text-muted">
                            <tbody className='user-data'>
                                <tr>
                                <th scope="row">SAP</th>
                                <td>{user.sap}</td>
                                </tr>
                                <tr>
                                <th scope="row">Email</th>
                                <td>{user.email}</td>
                                </tr>
                                <tr>
                                <th scope="row">Phone</th>
                                <td>{user.tel_no}</td>
                                </tr>
                                
                            </tbody>
                            </table>
                        </div>
                    </div>
                    <div className="row mt-2">
                        <Link to={`/edit-user/${user.id}`} className='col m-1 p-0'>
                            <button className="btn btn-success col-12" width='100%'>Edit</button>
                        </Link>
                        
                        <button 
                        className={`btn ${user.status==='inactive'?'btn-secondary':'btn-danger'} col m-1`} 
                        onClick={deactivate}
                        disabled={user.status==='inactive'?true:false}
                        >Delete
                        </button>
                    </div>
                </div>
            </div>
  )
}

export default PersonalDetails
