import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Loading from '../../assets/images/loading.gif';
import Swal from 'sweetalert2';
import { responseHandler } from '../../components/api/logics';
import { client } from '../../Constant';
import Pagination from '../../components/Pagination';

const ViewCategories = () => {
    const tableHeaders = ['CATEGORY NAME','ACTION'];
    const [recordsPerPage, setRecordsPerPage] = useState(10);
    const [currentPage, setCurrentPage] = useState(1);
    const [catData,setCatData] = useState([]);


    useEffect(()=>{
        const fetchData = async () => {
        try {
            const response = await getCat();
            setCatData(response); 
        } catch (error) {
            console.log(error);
        }
    };
        
    fetchData();
    },[catData]);
    const getCat = async()=>{
        try {
            const response = await client.get(`/eqpcat/get-all/`);
            return response.data;
        } catch (error) {
            //console.log(error);
            Swal.fire({
                title:error.message,
                
                icon:'error',
                confirmButtonText:'OK'
            })
            return error.message;
        }
        
    }
   
    const handleSizeChange = (e) => {
        const selectedValue = parseInt(e.target.value, 10);
        setRecordsPerPage(selectedValue);
        setCurrentPage(1); 
    };

    let records ;
    let numbers;
    let nPage;
    if(catData !==null && Array.isArray(catData)){
        const lastIndex = currentPage * recordsPerPage;
        const firstIndex = lastIndex - recordsPerPage;
        records = catData.slice(firstIndex, lastIndex) ||[];
        nPage = Math.ceil(catData.length / recordsPerPage);
        numbers = [...Array(nPage + 1).keys()].slice(1);

    }

    /// delete jobs
    const del =(id)=>{
      
        Swal.fire({
          title:'Delete',
          titleText:"Do you want to DELETE this Category?",
          icon:'question',
          confirmButtonText:'Yes',
          showCancelButton:true
        })
        .then(async(res)=>{
          try {
            if(res.isConfirmed){
              const response = await client.delete(`/eqpcat/${id}`);
              responseHandler(response.data);
              window.location.reload();
            }
          } catch (error) {
            const err= {error:error.message}
            responseHandler(err);
          }
        })
        
      
    }

    if(catData.length<=0){
        return <div className='d-flex justify-content-center align-items-center'>
            <img src={Loading} alt="Loading..." width={70} height={70} />
            </div>;
    }
    if (!Array.isArray(catData)) {
        return <div className='h4 text-center'>No data available.</div>;
    }
  return (
    <div>
        <div className="page-title-box d-flex justify-content-between">
            <h4 className="page-title ">Add Equipment Category</h4>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb">
                    <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
                    <li className="breadcrumb-item">Equipment Category</li>
                    <li className="breadcrumb-item active" aria-current="page">View Equipment Categories</li>
                </ol>
            </nav>
        </div>
        <div className="content">
            <form action="" className="row">
            <div className="col-2">
                <div className="d-flex align-items-baseline">
                <label htmlFor="" className="">Show Entries:</label>
                <select
                    className="form-select en-numbers "
                    onChange={handleSizeChange}
                >
                    <option value="10">10</option>
                    <option value="25">25</option>
                    <option value="50">50</option>
                    <option value="100">100</option>
                </select>
                </div>
            </div>
            </form>
            <div className="col mt-4">
            <table className="table table-hover">
                <thead>
                <tr>
                    <th>#</th>
                    {tableHeaders.map((item, index) => (
                    <th key={index} className={item=='ACTION'?'text-end':''}>{item}</th>
                    ))}
                    
                    
                </tr>
                </thead>
                <tbody>
                {Array.isArray(records) && records.map((item, index) => (
                    <tr key={index}>
                    <td>{index+1}</td>
                    <td><Link to={`/view-eqp-cat/${item.category}`}>{item.category}</Link></td>
                    
                    <td className='text-end'>
                        <Link to={`/edit-eqp-cat/${item.id}/${item.category}`}>
                        <button className="btn edit"><i className="bi bi-pencil-square"></i></button>
                        </Link>
                        
                        <button className="btn text-danger" onClick={()=>{del(item.id)}}><i className="bi bi-trash"></i></button>
                    </td>
                    
                    </tr>
                ))}
                </tbody>
            </table>
            </div>

            <Pagination
            nPage={nPage}
            pageNumbers={numbers}
            currentPage={currentPage}
            setCurrentPage={setCurrentPage}
            totalEntries={catData.length}
            itemsPerPage={recordsPerPage}
            />
        </div>
    </div>
  )
}

export default ViewCategories