import React from 'react';

const Pagination = ({ nPage, currentPage, setCurrentPage, totalEntries, itemsPerPage }) => {
  

  const generatePageNumbers = () => {
    const pages = [];
    const pageRange = 2; // Number of pages to show before and after the current page

    for (let i = 1; i <= nPage; i++) {
      if (i === 1 || i === nPage || (i >= currentPage - pageRange && i <= currentPage + pageRange)) {
        pages.push(i);
      } else if (
        pages[pages.length - 1] !== '...' &&
        (i < currentPage - pageRange || i > currentPage + pageRange)
      ) {
        pages.push('...');
      }
    }

    return pages;
  };

  const prePage = () => {
    if (currentPage > 1) {
      setCurrentPage(currentPage - 1);
    }
  };

  const nextPage = () => {
    if (currentPage < nPage) {
      setCurrentPage(currentPage + 1);
    }
  };

  const changePage = (pageNumber) => {
    if (pageNumber !== '...') {
      setCurrentPage(pageNumber);
    }
  };

  const getStartIndex = () => {
    return (currentPage - 1) * itemsPerPage + 1;
  };

  const getEndIndex = () => {
    const endIndex = currentPage * itemsPerPage;
    return endIndex > totalEntries ? totalEntries : endIndex;
  };

  return (
    <div className='d-flex align-items-baseline justify-content-between'>
      <p>{`Showing ${getStartIndex() || 0} to ${getEndIndex() || 0} of ${totalEntries ||0} entries`}</p>
      <nav>
        <ul className="pagination">
          <li className="page-item">
            <a href="#" className="page-link" onClick={prePage}>
              Prev
            </a>
          </li>
          {generatePageNumbers().map((page, index) => (
            <li key={index} className={`page-item ${currentPage === page ? 'active' : ''}`}>
              <a href="#" className="page-link" onClick={() => changePage(page)}>
                {page}
              </a>
            </li>
          ))}
          <li className="page-item">
            <a href="#" className="page-link" onClick={nextPage}>
              Next
            </a>
          </li>
        </ul>
      </nav>
    </div>
  );
};

export default Pagination;
