import React, { useEffect, useState } from 'react'
import { responseHandler } from '../../components/api/logics';
import { client } from '../../Constant';
import { Link, useParams } from 'react-router-dom';
import TextInput2 from '../../components/formComponents/TextInput2';

const EditCat = () => {
    const [name,setName] = useState('');
    const {id,catName} = useParams();
    const catId =+id;
    
    useEffect(()=>{
        setName(catName);
    },[])
    //post data
    const handleSubmit=async()=>{
        try {
            const response = await client.put(`/eqpcat/${catId}`,{category:name});
            responseHandler(response.data);
        } catch (error) {
            responseHandler({error:error.message});
        }
    }
  return (
    <div>
        <div className="page-title-box d-flex justify-content-between">
            <h4 className="page-title ">Add Equipment Category</h4>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb">
                    <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
                    <li className="breadcrumb-item">Equipment Category</li>
                    <li className="breadcrumb-item active" aria-current="page">Add Equipment Category</li>
                </ol>
            </nav>
        </div>
        <div className="content">
            <div className="row">
                <TextInput2 
                label={'Category name'}
                type={'text'}
                name={'cat_name'}
                value={name}
                onchange={(e)=>setName(e.target.value)}
                />
                <button className='btn btn-primary col-1' onClick={handleSubmit}>Submit</button>
            </div>
        </div>
    </div>
  )
}

export default EditCat