import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Pagination from '../../components/Pagination';
import './user.css';
import { client } from '../../Constant';
import Swal from 'sweetalert2';
import Loading from '../../assets/images/loading.gif';
import { responseHandler } from '../../components/api/logics';

const ArchivedUsers = () => {
  const tableHeaders = ['NAME', 'USERNAME', 'TYPE', 'CONTACT', 'SAP NUMBER', 'DEPARTMENT'];
  const [recordsPerPage, setRecordsPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);
  const [userData,setUserData] = useState([])
  useEffect(()=>{
    const fetchData = async () => {
      try {
          const response = await getUsers();
          setUserData(response); 
      } catch (error) {
          console.log(error);
      }
  };
    
  fetchData();
  },[]);
  const getUsers = async()=>{
    try {
        const response = await client.get(`/users/inactive`);
        return response.data;
    } catch (error) {
        //console.log(error);
        Swal.fire({
            title:error.message,
            
            icon:'error',
            confirmButtonText:'OK'
        })
        return error.message;
    }
    
  }

  

  const handleSizeChange = (e) => {
    const selectedValue = parseInt(e.target.value, 10);
    setRecordsPerPage(selectedValue);
    setCurrentPage(1); // Reset current page to 1 when changing the records per page
  };

  const activateUser =async (id) => {

    console.log("FN triggered")
    //call api
    Swal.fire({
      title: `Do you want to ACTIVATE this profile?`,
      icon:'question',
      showCancelButton: true,
      confirmButtonText: "Yes",
     
    }).then(async(result) => {
      /* Read more about isConfirmed, isDenied below */
      if (result.isConfirmed) {
          try {
              const response = await client.put(`/users/change-status/:${id}`,{
                  id:id,
                  status:'active'
              });
              responseHandler(response.data);
          } catch (error) {
              const response= {error:error.message}
              responseHandler(response)
          }
          window.location.href='/archived-users';
      } 
    });
    
  };

  const lastIndex = currentPage * recordsPerPage;
  const firstIndex = lastIndex - recordsPerPage;
  const records = userData.slice(firstIndex, lastIndex) ||[];
  const nPage = Math.ceil(userData.length / recordsPerPage);
  const numbers = [...Array(nPage + 1).keys()].slice(1);

  if(userData.length<=0){
    return <div className='d-flex justify-content-center align-items-center'>
          <img src={Loading} alt="Loading..." width={70} height={70} />
        </div>;
  }
  if (!Array.isArray(userData)) {
    return <div className='h4 text-center'>No data available.</div>;
  }


  return (
    <div className='view-user-container'>
      
      <div className="content">
        
        <div className="col mt-4">
          <table className='table table-hover'>
            <thead>
              <tr>
                <th>#</th>
                {tableHeaders.map((item, index) => (
                  <th key={index}>{item}</th>
                ))}
                <th>ACTION</th>
              </tr>
            </thead>
            <tbody>
              {records.map((item, index) => (
                <tr key={index}>
                  <td>{item.id}</td>
                  <td><Link to={`/view-user/${item.id}`}>{item.name}</Link></td>
                  <td>{item.user_name}</td>
                  <td>{item.user_type}</td>
                  <td>{item.tel_no}</td>
                  <td>{item.sap}</td>
                  <td>{item.departments}</td>
                  <td>
                    <button className='btn' onClick={() => activateUser(item.id)}>Activate</button>
                    
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        <Pagination
          nPage={nPage}
          pageNumbers={numbers}
          currentPage={currentPage}
          setCurrentPage={setCurrentPage}
          totalEntries={userData.length}
          itemsPerPage={recordsPerPage}
        />
      </div>
    </div>
  );
};

export default ArchivedUsers;
