import React from 'react'

const TextInput2 = ({ label,type, name,value, onchange }) => {
  return (
    <div className="col-6">
      <div className="d-flex align-items-baseline">
        <label htmlFor={name} className='col-3 fw-bold'>{label}:</label>
        <input type={type} id={name} className='form-control col' onChange={onchange} value={value} />
      </div>
    </div>
  )
}

export default TextInput2
