import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Pagination from '../../components/Pagination';
import './user.css';
import { client } from '../../Constant';
import Swal from 'sweetalert2';
import Loading from '../../assets/images/loading.gif';
import { responseHandler } from '../../components/api/logics';



const ViewUsers = () => {
  const tableHeaders = ['NAME', 'USERNAME', 'TYPE', 'CONTACT', 'SAP NUMBER', 'DEPARTMENT'];
  const [recordsPerPage, setRecordsPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);
  const [userData,setUserData] = useState([])
  useEffect(()=>{
    const fetchData = async () => {
      try {
          const response = await getUsers();
          setUserData(response); 
      } catch (error) {
          console.log(error);
      }
  };
    
  fetchData();
  },[]);
  const getUsers = async()=>{
    try {
        const response = await client.get(`/users/`);
        return response.data;
    } catch (error) {
        //console.log(error);
        Swal.fire({
            title:error.message,
            
            icon:'error',
            confirmButtonText:'OK'
        })
        return error.message;
    }
    
  }

  

  const handleSizeChange = (e) => {
    const selectedValue = parseInt(e.target.value, 10);
    setRecordsPerPage(selectedValue);
    setCurrentPage(1); // Reset current page to 1 when changing the records per page
  };

  const toggleStatus =async (id,status) => {

    let currentStatus = status;
    let text = 'ACTIVATE'
    if(status === 'active'){
      currentStatus ='inactive';
      text="DEACTIVATE"
    }else{
      currentStatus='active'
    }
    //call api
    Swal.fire({
      title: `Do you want to ${text} this profile?`,
      icon:'question',
      showCancelButton: true,
      confirmButtonText: "Yes",
     
    }).then(async(result) => {
      /* Read more about isConfirmed, isDenied below */
      if (result.isConfirmed) {
          try {
              const response = await client.put(`/users/change-status/:${id}`,{
                  id:id,
                  status:currentStatus
              });
              responseHandler(response.data);
          } catch (error) {
              const response= {error:error.message}
              responseHandler(response)
          }
          window.location.href='/view-users';
      } 
    });
    
  };

  const lastIndex = currentPage * recordsPerPage;
  const firstIndex = lastIndex - recordsPerPage;
  const records = userData.slice(firstIndex, lastIndex) ||[];
  const nPage = Math.ceil(userData.length / recordsPerPage);
  const numbers = [...Array(nPage + 1).keys()].slice(1);

  if(userData.length<=0){
    return <div className='d-flex justify-content-center align-items-center'>
          <img src={Loading} alt="Loading..." width={70} height={70} />
        </div>;
  }
  if (!Array.isArray(userData)) {
    return <div className='h4 text-center'>No data available.</div>;
  }

  return (
    <div className="view-user-container">
      <div className="page-title-box d-flex justify-content-between">
        <h4 className="page-title">View User</h4>
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
            <li className="breadcrumb-item"><a href="#">User</a></li>
            <li className="breadcrumb-item active" aria-current="page">View User</li>
          </ol>
        </nav>
      </div>
      <div className="content">
        <form action="" className="row">
          <div className="col-2">
            <div className="d-flex align-items-baseline">
              <label htmlFor="" className="col">Show Entries:</label>
              <select
                name=""
                id=""
                className="form-select en-numbers"
                onChange={handleSizeChange}
              >
                <option value="10">10</option>
                <option value="25">25</option>
                <option value="50">50</option>
                <option value="100">100</option>
              </select>
            </div>
          </div>
        </form>
        <div className="col mt-4">
          <table className="table table-hover">
            <thead>
              <tr>
                <th>#</th>
                {tableHeaders.map((item, index) => (
                  <th key={index}>{item}</th>
                ))}
                <th>ACTION</th>
                <th>STATUS</th>
              </tr>
            </thead>
            <tbody>
              {records.map((item, index) => (
                <tr key={index}>
                  <td>{item.id}</td>
                  <td><Link to={`/view-user/${item.id}`}>{item.name}</Link></td>
                  <td>{item.user_name}</td>
                  <td>{item.user_type}</td>
                  <td>{item.tel_no}</td>
                  <td>{item.sap}</td>
                  <td>{item.departments}</td>
                  <td>
                    <button className="btn edit"><i className="bi bi-pencil-square"></i></button>
                  </td>
                  <td>
                    <div className="form-check form-switch">
                      <input
                        className="form-check-input"
                        type="checkbox"
                        role="switch"
                        id={`flexSwitchCheckChecked${index}`}
                        checked={item.status === 'active'}
                        onChange={() => toggleStatus(item.id,item.status)}
                      />
                      <label className="form-check-label" htmlFor={`flexSwitchCheckChecked${index}`}>{item.status || 'Deactivate'}</label>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        <Pagination
          nPage={nPage}
          pageNumbers={numbers}
          currentPage={currentPage}
          setCurrentPage={setCurrentPage}
          totalEntries={userData.length}
          itemsPerPage={recordsPerPage}
        />
      </div>
    </div>
  );
}

export default ViewUsers;
