
import React,{useState} from 'react';
import './header.css';
import Avatar from '../../assets/images/user-avatar.png';
import Logo  from '../../assets/images/side_bar_logo.png';
import { Link } from 'react-router-dom';



const Header = () => {
    
    const username = 'Pasindu';
    const role = 'Super Admin';

    const [isOpen, setIsOpen] = useState(false);
    
    const toggleModal = () => {
      setIsOpen(!isOpen);
    };
    
  
  return (
    <header className='d-flex justify-content-between align-items-center border-bottom'>
      <div className="logo" >
        <img src={Logo} alt="logo" height='70px' />
      </div>
      <div className="menu">
        <div className="search me-3">
          <input type="text" name="" id="search-input" placeholder='Search...' />
          <i className="bi bi-search"></i>
        </div>
        <div className="notification me-3">
          <i className="bi bi-bell"></i>
          <sup><span className="badge text-bg-danger">4</span></sup>
        </div>
        <img src={Avatar} alt="user-avatar" />
        <div className="col user">
            <div className="name">{username}</div>
            <div className="role">{role}</div>
        </div>
        <div className="settings ">
            <i className="bi bi-gear" onClick={toggleModal}></i>
            {/**dropdown model */}
            {isOpen &&(
                <div className="model">
                    <div className='text-center'>Welcome!</div>
                    <div className='m-3'>
                      <Link to={'/edit-profile/1'} >
                        <i className="bi bi-gear mr-2"></i>
                        <span className='logout-text'>Settings</span>
                      </Link>
                      
                    </div>
                    <hr />
                    <div className='d-flex p-3 '>
                        <svg width="16" height="16" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-log-out icon-dual icon-xs me-1"><g><path d="M9 21H5a2 2 0 0 1-2-2V5a2 2 0 0 1 2-2h4"></path>
                        <polyline points="16 17 21 12 16 7"></polyline><line x1="21" y1="12" x2="9" y2="12"></line></g></svg>
                        <div className='logout-text'>Logout</div>
                    </div>
                
            </div>
            )}
            
        </div>
      </div>
    </header>
  )
}

export default Header

