import React,{useEffect, useState} from 'react';
import './job.css';
import Select from 'react-select';
import RadioInput from '../../components/formComponents/RadioInput';
import { useForm, Controller } from 'react-hook-form';
import { useParams } from 'react-router-dom';
import { client } from '../../Constant';
import Swal from 'sweetalert2';
import Loading from '../../assets/images/loading.gif';
import { responseHandler } from '../../components/api/logics';
import TextInput2 from '../../components/formComponents/TextInput2';

const EditJob = () => {
    const { register, handleSubmit, control } = useForm();
    const [inputFields, setInputFields] = useState([{ id: 1 }]);
    const [multiImage, setMultiImage] = useState([]);
    const [job, setJob] = useState(null);
    const [loc, setLoc] = useState(null);
    const [eqp, setEqp] = useState(null);
    const [comment ,setComment]=useState('');
    const [deadline ,setDeadline]=useState('');
    const [locValue, setLocVal] = useState({
      value: '',
      label:''
    });
    const [eqpValue, setEqpVal] = useState({
      value: '',
      label:''
    });
    const [floorVal, setFloorVal] = useState({
      value:'',
      label:''
    })
    const {id}=useParams();
    const jobId =+id;
    
    // floor select
    const floors = [
        { label: 'TM', value: 'TM' },
        { label: '1', value: '1' },
        { label: '2', value: '2' },
        { label: '3', value: '3' },
        { label: '4', value: '4' },
        { label: '5', value: '5' },
        { label: '6', value: '6' },
        { label: '7', value: '7' },
        { label: '8', value: '8' },
        { label: '9', value: '9' },
        { label: '10', value: '10' },
        { label: '11', value: '11' },
        { label: '12', value: '12' },
        { label: '13', value: '13' },
        { label: '14', value: '14' },
        { label: '15', value: '15' },
        { label: '16', value: '16' },
        { label: '17', value: '17' },
        { label: '18', value: '18' },
        { label: '19', value: '19' },
        { label: '20', value: '20' },
        { label: '21', value: '21' },
        { label: '22', value: '22' },
        { label: '23', value: '23' },
        { label: '24', value: '24' },
        { label: '25', value: '25' },
        { label: '26', value: '26' },
        { label: '27', value: '27' },
        
        
    ];

    // fetching data from api
    useEffect(()=>{
      const fetchJob = async()=>{
        const response = await getJob(jobId);
        setJob(response);

        const locations = await getLoc();
        setLoc(locations);

        const eqp = await getEqp();
        setEqp(eqp);

      }
      fetchJob()
    },[]);
    //get job
    const getJob =async(id)=>{
        try {
            const response = await client.get(`/jobs/${id}`);
            return response.data;
        } catch (error) {
            Swal.fire({
                title:error.message,
                
                icon:'error',
                confirmButtonText:'OK'
            })
            //return error.message;
        }
    }
    //get location
    const getLoc =async()=>{
        try {
            const response = await client.get(`/location/get-all`);
            return response.data;
        } catch (error) {
            Swal.fire({
                title:error.message,
                
                icon:'error',
                confirmButtonText:'OK'
            })
            return error.message;
        }
    }
    //get equipment
    const getEqp =async()=>{
        try {
            const response = await client.get(`/equipment/get-all`);
            return response.data;
        } catch (error) {
            Swal.fire({
                title:error.message,
                
                icon:'error',
                confirmButtonText:'OK'
            })
            return error.message;
        }
    }

    

    let jobData=[];
    let eqpData=[];
    let locSelect =[];
  if(Array.isArray(job)){
    jobData = job[0];
    
  }
  if(Array.isArray(loc)){
    loc.map((loc)=>{
      locSelect.push({
        value:loc.room,
        label:`${loc.room}(${loc.location_type})`
      })
    })
  }
  if(Array.isArray(eqp)){
    eqp.map((e)=>{
      eqpData.push({
        value:e.name,
        label:`${e.name}`
      })
    });
  }

  useEffect(() => {
    if (Array.isArray(job) && job.length > 0) {
      const location = locSelect.find((l) => l.value === job[0].location);
      setLocVal({
        value: location?.value || '',
        label: location ? location.label : ''
      });

      const eqpSelect = eqpData.find((e)=>e.value === job[0].equipment);
      setEqpVal({
        value: eqpSelect?.value || '',
        label: eqpSelect ? eqpSelect.label : ''
      });
      setComment(job[0].comments);
      setDeadline(job[0].deadline)
      
      
    }
    
  }, [job, loc,eqp]);

  useEffect(() => {
    if (Array.isArray(job) && job.length > 0) {
    const floorSelect = floors.find((e)=>e.value === job[0].floor_no);
      setFloorVal([{value:floorSelect?.value, label:floorSelect?.label}]);
    
    }
  }, [eqp]);
  
  //console.log(floorVal)

  
  

    const addInputField = () => {
        if (inputFields.length < 5) {
        const newInputFields = [...inputFields, { id: inputFields.length + 1 }];
        setInputFields(newInputFields);
        } else {
        alert('You can only add up to 5 input fields.');
        }
    }

    const removeInputField = (idToRemove) => {
        
        const updatedInputFields = inputFields.filter((field) => field.id !== idToRemove);
        setInputFields(updatedInputFields);
    }
    //handle multiple images
    const handleImageUpload =async (e) => {
        const imageSt =  e.target.files[0];
        setMultiImage([...multiImage,imageSt]);
    }

    // update form
    const onSubmit=async(data)=>{
      
      const response = await update(data);
      responseHandler(response);
      
      
    }
    //update
    const update =async(data)=>{
      try {
        const formData = new FormData();
      
        formData.append('critical',data.critical);
        formData.append('floor_no',floorVal.value);
        formData.append('location',locValue.value);
        formData.append('equipment',eqpValue.value);
        formData.append('comments',comment);
        formData.append('date',deadline);

        multiImage.map((image)=>{
          formData.append('picture[]',image);
        });

        const response = await client.put(`/jobs/${jobId}`,formData, {
          headers:{'Content-Type': 'multipart/form-data'}
        });
        return response.data;
      } catch (error) {
        return {error:error.message}
      }
    }
    if(job=== null){
      return <div className='d-flex justify-content-center align-items-center'>
          <img src={Loading} alt="Loading..." width={70} height={70} />
          </div>;
    }
    if (!Array.isArray(job)) {
        return <div className='h4 text-center'>No data available.</div>;
    }
  return (
    <div className='add-job-container'>
      <div className="heading d-flex justify-content-between">
        <div className="page-title"><h4>Edit Job</h4></div>
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><a href="#">Home</a></li>
            <li className="breadcrumb-item"><a href="#">Jobs</a></li>
            <li className="breadcrumb-item"><a href="#">View Jobs</a></li>
            <li className="breadcrumb-item active" aria-current="page">Edit</li>
          </ol>
        </nav>
      </div>
      <div className="col bg-white content">
        <form action="" className='col' onSubmit={handleSubmit(onSubmit)}>
          <div className="d-flex justify-content-between">
            
            <div className="col">
              <div className="row">
                <label htmlFor="critical" className='col-3 fw-bold'>Critical:</label>
                <div className="col">
                  <div className="d-flex">
                    {['Low', 'Medium', 'High'].map(value => (
                      <RadioInput
                        key={value}
                        label={value}
                        name={'critical'}
                        value={value}
                        register={register}
                        checked={value === jobData?.critical}
                      />
                    ))}
                  </div>
                </div>
              </div>
              
            </div>
            
            {/* Floor No */}
            <div className="col">
              <div className="row">
                <label htmlFor="user-type" className='col-3 fw-bold'>Floor No:</label>
                <Controller
                  name="floor"
                  control={control}
                  render={({ field }) => (
                    <Select
                      className='col-6 p-0'
                      {...field}
                      options={floors}
                      isSearchable={true}
                      placeholder="Select floor"
                      value={floorVal ||[]}
                      onChange={(selectedValue)=>{setFloorVal(selectedValue)}}

                    />
                  )}
                />
              </div>
            </div>
          </div>
          {/* Location */}
          <div className="d-flex justify-content-between mt-3">
            <div className="col-6">
              <div className="row">
                <label htmlFor="user-type" className='col-3 fw-bold'>Location:</label>
                <Controller
                  name="location"
                  control={control}
                  render={({ field }) => (
                    <Select
                      className='col-6 p-0'
                      {...field}
                      options={locSelect}
                      isSearchable={true}
                      placeholder="Select location"
                      value={locValue ||[]}
                      onChange={(selectedValue)=>{setLocVal(selectedValue)}}
                    />
                  )}
                />
              </div>
            </div>
            {/* Equipment */}
            <div className="col-6">
              <div className="row">
                <label htmlFor="user-type" className='col-3 fw-bold'>Equipment:</label>
                <Controller
                  name="equipment"
                  control={control}
                  render={({ field }) => (
                    <Select
                      className='col-6 p-0'
                      {...field}
                      options={eqpData}
                      isSearchable={true}
                      placeholder="Select equipment"
                      value={eqpValue||[]}
                      onChange={(selectedValue)=>{setEqpVal(selectedValue)}}
                    />
                  )}
                />
              </div>
            </div>
          </div>
          {/* Comment */}
          <div className="d-flex justify-content-between mt-3">
            <div className="col-6">
              <div className="row">
                <label htmlFor="comment" className='col-3 fw-bold'>Comment:</label>
                <textarea 
                className='form-control col-6'  cols="10" rows="5" 
                value={comment || ''} 
                onChange={(e)=>setComment(e.target.value)}
                ></textarea>
              </div>
            </div>
            {/* Photos */}
            <div className="col-6">
              {inputFields.map((field, index) => (
                <div className="row" key={index}>
                  <label htmlFor="images" className='col-3 fw-bold'>Photos:</label>
                  <input className="form-control col-6" type="file" id="formFileMultiple" multiple 
                    onChange={handleImageUpload}
                  />
                  {inputFields.length < 5 && (
                    <button type='button' className="btn text-success col-1 " onClick={addInputField}><i className="bi bi-plus-circle-fill h4"></i></button>
                  )}
                  {field.id != 1?<button type='button' className='btn text-danger col-1' onClick={()=>{removeInputField(field.id)}}><i className="bi bi-x-circle-fill h4"></i></button>:''}
                  
                </div>
              ))}
              
            </div>
          </div>
          {/* Additional Fields... */}
          <div className="d-flex justify-content-between mt-3">
            <div className="col-6">
                <div className="row">
                  <label htmlFor="comment" className='col-3 fw-bold'>Deadline:</label>
                  <input
                  type='date' 
                  className='form-control col-6'   
                  value={deadline} 
                  onChange={(e)=>deadline(e.target.value)}
                  />
                </div>
            </div>
          </div>
          <div className="d-flex justify-content-between mt-3">
            <div className="col-12">
              <button type="submit" className='btn btn-primary'>Submit</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  )
}

export default EditJob
