import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import Pagination from '../../components/Pagination';
import Swal from 'sweetalert2';
import { responseHandler } from '../../components/api/logics';
import { client } from '../../Constant';
const ViewCategory = () => {
    const {eqp} =useParams();
    const tableHeaders = ['CODE NO','NAME','CATEGORY','MAINTENANCE START DATE','LAST MAINTAINED DATE','NEXT SCHEDULED DATE','ACTION'];
    const [recordsPerPage, setRecordsPerPage] = useState(10);
    const [currentPage, setCurrentPage] = useState(1);
    const [catData,setCatData] = useState([]);
    
    //get list
    useEffect(()=>{
        const fetch =async()=>{
            try{
                const response = await getList();
                setCatData(response);
            }finally{}
        }
        fetch();
    },[catData]);

    //get list
    const getList =async()=>{
        try {
            const response = await client.get(`/eqpcat/list/${eqp}`);
            return response.data;
        } catch (error) {
            Swal.fire({
                title:error.message,
                
                icon:'error',
                confirmButtonText:'OK'
            })
        }
    }

    const handleSizeChange = (e) => {
        const selectedValue = parseInt(e.target.value, 10);
        setRecordsPerPage(selectedValue);
        setCurrentPage(1); 
    };

    let records ;
    let numbers;
    let nPage;
    if(catData !==null && Array.isArray(catData)){
        const lastIndex = currentPage * recordsPerPage;
        const firstIndex = lastIndex - recordsPerPage;
        records = catData.slice(firstIndex, lastIndex) ||[];
        nPage = Math.ceil(catData.length / recordsPerPage);
        numbers = [...Array(nPage + 1).keys()].slice(1);

    }

    /// delete jobs
    const del =(id)=>{
      
        Swal.fire({
          title:'Delete',
          titleText:"Do you want to DELETE this Category?",
          icon:'question',
          confirmButtonText:'Yes',
          showCancelButton:true
        })
        .then(async(res)=>{
          try {
            if(res.isConfirmed){
              const response = await client.delete(`/eqpcat/${id}`);
              responseHandler(response.data);
              window.location.reload();
            }
          } catch (error) {
            const err= {error:error.message}
            responseHandler(err);
          }
        })
        
      
    }
  return (
    <div>
        <div className="page-title-box d-flex justify-content-between">
            <h4 className="page-title ">{eqp.toLocaleUpperCase()}</h4>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb">
                    <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
                    <li className="breadcrumb-item">Equipment Category</li>
                    <li className="breadcrumb-item active" aria-current="page">View Equipment Category</li>
                </ol>
            </nav>
        </div>
        <div className="content">
            <div className="row">
                <div className="col-2">
                    <div className="d-flex align-items-baseline">
                    <label htmlFor="" className="col-6">Show Entries:</label>
                    <select
                        className="form-select"
                        onChange={handleSizeChange}
                    >
                        <option value="10">10</option>
                        <option value="25">25</option>
                        <option value="50">50</option>
                        <option value="100">100</option>
                    </select>
                    </div>
                </div>
            </div>
            <div className="col mt-4">
                <table className="table table-hover">
                    <thead>
                    <tr>
                        <th>#</th>
                        {tableHeaders.map((item, index) => (
                        <th key={index} className={item=='ACTION'?'text-end':''}>{item}</th>
                        ))}
                        
                        
                    </tr>
                    </thead>
                    <tbody>
                    {Array.isArray(records) && records.map((item, index) => (
                        <tr key={index}>
                            <td>{index+1}</td>
                            <td><Link to={`/view-eqp-cat/${item.category}`}>{item.code_no}</Link></td>
                            <td>{item.name}</td>
                            <td>{item.category}</td>
                            <td>{item.start_date}</td>
                            <td>{item.last_date}</td>
                            <td>{item.next_date}</td>
                            <td className='text-end'>
                                <Link to={`/edit-eqp-cat/${item.id}/${item.category}`}>
                                <button className="btn edit"><i className="bi bi-pencil-square"></i></button>
                                </Link>
                                
                                <button className="btn text-danger" onClick={()=>{del(item.id)}}><i className="bi bi-trash"></i></button>
                            </td>
                            
                        </tr>
                    ))}
                    
                    </tbody>
                </table>
            </div>
            {records.length===0 && (
                        
                <div className="h3 text-center">No data.</div>
                   
            )}

            <Pagination
            nPage={nPage}
            pageNumbers={numbers}
            currentPage={currentPage}
            setCurrentPage={setCurrentPage}
            totalEntries={catData.length}
            itemsPerPage={recordsPerPage}
            />
        </div>
    </div>
  )
}

export default ViewCategory