import React, { useEffect, useState } from 'react';
import TextInput2 from '../../components/formComponents/TextInput2';
import Select from 'react-select';
import { Link, useParams } from 'react-router-dom';
import Loading from '../../assets/images/loading.gif';
import { client } from '../../Constant';
import { responseHandler } from '../../components/api/logics';
const EditLocation = () => {
  const [location,setLocation] = useState(null);
  const [locationType,setLocationType] = useState([{
      value:'',
      label:''
  
  }]);
  const [room,setRoom]=useState('');
  const [floor,setFloor]=useState('');
  const [desc,setDesc] = useState('');
  const [loading,setLoading] = useState(false);
  
  // loc types
  const locType = [
    {
      value:"Common areas",
      label:"Common areas"
    },
    {
      value:"Rooms",
      label:"Rooms"
    }
  ];
  const {id} = useParams();
  const locId = +id;
  //fetch data
  useEffect(()=>{
    const fetch=async()=>{
      try{
        setLoading(true);
        const response = await getLoc();
        
        if(!Array.isArray(response)){
          responseHandler(response);
          return;
        }
        setLocation(response);
      }finally{
        setLoading(false);
      }
    }
    fetch();
  },[]);

  //load data to form
  useEffect(()=>{
    if(location !== null && Array.isArray(location)){
        location.forEach((loc)=>{
          setLocationType( {value:loc.location_type, label:loc.location_type});
          setRoom(loc.room);
          setFloor(loc.floor_no);
          setDesc(loc.description)
        })
        
        
    }
  },[location])
  //get api response
  const getLoc = async()=>{
    
    try {
      const response = await client.get(`/location/${locId}`);
      return response.data;
    } catch (error) {
      return {error:error.message}
    }
  }
  const handleSubmit=async()=>{
    
    
    try{
      setLoading(true);
      const response = await update(locId);
      responseHandler(response);
    }finally{
      setLoading(false);
    }
  }
  //update data
  const update =async(id)=>{
    const locationTypeValue = locationType.value;
    try {
      const response = await client.put(`/location/${id}`,{
        location_type:locationTypeValue,
        room:room,
        floor_no:floor,
        description:desc
      });
      return response.data;
      
    } catch (error) {
      return {error:error.message}
    }
  }
  return (
    <div>
      <div className="page-title-box d-flex justify-content-between">
        <h4 className="page-title ">Edit Location</h4>
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
            <li className="breadcrumb-item">Locations</li>
            <li className="breadcrumb-item"><Link to={'/view-locations'}>View Locations</Link></li>
            <li className="breadcrumb-item active" aria-current="page">Edit Location</li>
          </ol>
        </nav>
      </div>
      <div className="content">
        <div className="col">
            <form action="" className='col' >
              <div className="row">
                <div className="col-6">
                  <div className="row mt-3">
                    <label htmlFor="loc_type" className='col-3 fw-bold'>Location Type:</label>
                        <Select
                          className='col-6 p-0'
                          options={locType}
                          isSearchable={true}
                          placeholder="Select User Type"
                          value={locationType}
                          onChange={(e)=>setLocationType(e)}
                        />
                     
                  </div>
                  
                  
                </div>
                
                <TextInput2
                  
                    label={"Room name or no"}
                    type={"text"}
                    name={"room"}
                    value={room}
                    onchange={(e)=>setRoom(e.target.value)}
                  />
              </div>
              <div className="row mt-3">
                <div className="col-6">
                  <div className="row">
                    <label htmlFor="floor_no" className='col-3 fw-bold'>Floor No:</label>
                    <input 
                    type="text" 
                    className='form-control col-6'  
                    value={floor}
                    onChange={(e)=>setFloor(e.target.value)}
                    />
                  </div>
                </div>
                <div className="col-6">
                  <div className="row">
                    <label htmlFor="description" className='col-3 fw-bold'>Description:</label>
                    <textarea 
                    name=""  
                    cols="10" 
                    rows="3" 
                    className='form-control col ms-2 me-2'
                    value={desc}
                    onChange={(e)=>setDesc(e.target.value)}
                    ></textarea>
                  </div>
                  
                </div>
              </div>
              <div className="col">
                <input type="button" onClick={handleSubmit} className='btn btn-primary' value=" Submit" />
              </div>
            </form>
            
        </div>
      </div>
      {loading && (
        <div className='waiting-model'>
            <img className='' src={Loading} width={50} height={50} alt="" />
        </div>
      )}
      
    </div>
  )
}

export default EditLocation
