import Swal from "sweetalert2";



export const responseHandler = (response)=>{
    if(response.error){
        Swal.fire({
            icon: "error",
            title: "Oops...",
            text: response.error,
            
        });
    }
    if(response.message){
        Swal.fire({
            position: "top-end",
            icon: "success",
            title: response.message,
            showConfirmButton: false,
            timer: 1500
        });
    }
}