import React,{useState} from 'react';
import { userData } from '../../../Data';
import Pagination from '../../Pagination';
import { Link } from 'react-router-dom';


const JobList = ({tableData}) => {
    const tableHeaders =['JOB NUMBER','JOB CATEGORY', 'DEADLINE','LOCATION','STATUS'];
    const [recordsPerPage, setRecordsPerPage] = useState(10);
    const [currentPage,setCurrentPage]=useState(1);

    const handleSizeChange = (e) => {
        const selectedValue = parseInt(e.target.value, 10);
        setRecordsPerPage(selectedValue);
        setCurrentPage(1); // Reset current page to 1 when changing the records per page
    };
    
    const lastIndex = currentPage*recordsPerPage;
    const firstIndex = lastIndex-recordsPerPage;
    const records = tableData.slice(firstIndex,lastIndex);
    const nPage = Math.ceil(tableData.length/recordsPerPage);
    const numbers = [...Array(nPage+1).keys()].slice(1);

  return (
    <div className="content a-border">
        <h4>JOB LIST</h4>
          <form action="" className='row'>
            <div className="col-3">
              <div className="d-flex align-items-baseline">
              <label htmlFor="" className='col'>Show Entries:</label>
              <select name="" id="" className='form-select en-numbers' 
             onChange={handleSizeChange}
              
              >
                <option value="10">10</option>
                <option value="25">25</option>
                <option value="50">50</option>
                <option value="100">100</option>
              </select>
              </div>
            </div>
          </form>
          <div className="col mt-4">
            <table className='table table-hover'>
                <thead>
                    <tr>
                      <th>#</th>
                        {
                          tableHeaders.map((item,index)=>(
                                <th key={index}>{item}</th>
                            ))
                        }
                      <th>EDIT</th>
                      
                    </tr>
                </thead>
                <tbody>
                    {records.map((job,index)=>(
                      <tr key={index}>
                        <td>{index+1}</td>
                        <td><Link to={`/view-job/${job.id}`}>{job.job_no}</Link></td>
                        <td>{job.category}</td>
                        <td>{job.deadline}</td>
                        <td>{job.location}</td>
                        
                        <td>{job.status}</td>
                        <td><Link to={`/edit-job/${job.id}`}>
                        <button className="btn edit"><i className="bi bi-pencil-square"></i></button>
                        </Link></td>
                      </tr>
                    ))}
                </tbody>
            </table>
          </div>

            <Pagination 
            nPage={nPage} 
            pageNumbers={numbers} 
            currentPage={currentPage}
            setCurrentPage={setCurrentPage}
            totalEntries={tableData.length}
            itemsPerPage={recordsPerPage}
            />

            
        </div>
  )
}

export default JobList
