import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Pagination from '../../components/Pagination';
import './job.css';
import { client } from '../../Constant';
import Swal from 'sweetalert2';
import Loading from '../../assets/images/loading.gif';
import { responseHandler } from '../../components/api/logics';

const ViewJobs = () => {
    const tableHeaders = ['JOB NO', 'JOB CATEGORY', 'DEADLINE', 'LOCATION', 'COMMENT', 'STATUS'];
    const [recordsPerPage, setRecordsPerPage] = useState(10);
    const [currentPage, setCurrentPage] = useState(1);
    const [JobData,setJobData] = useState([]);
    useEffect(()=>{
        const fetchData = async () => {
        try {
            const response = await getJobs();
            setJobData(response); 
        } catch (error) {
            console.log(error);
        }
    };
        
    fetchData();
    },[]);
    const getJobs = async()=>{
        try {
            const response = await client.get(`/jobs/get-all/`);
            return response.data;
        } catch (error) {
            //console.log(error);
            Swal.fire({
                title:error.message,
                
                icon:'error',
                confirmButtonText:'OK'
            })
            return error.message;
        }
        
    }
    const handleSizeChange = (e) => {
        const selectedValue = parseInt(e.target.value, 10);
        setRecordsPerPage(selectedValue);
        setCurrentPage(1); // Reset current page to 1 when changing the records per page
    };
    const lastIndex = currentPage * recordsPerPage;
    const firstIndex = lastIndex - recordsPerPage;
    const records = JobData.slice(firstIndex, lastIndex) ||[];
    const nPage = Math.ceil(JobData.length / recordsPerPage);
    const numbers = [...Array(nPage + 1).keys()].slice(1);


    /// delete jobs
    const del =(id)=>{
      
        Swal.fire({
          title:'Delete',
          titleText:"Do you want to DELETE this job?",
          icon:'question',
          confirmButtonText:'Yes',
          showCancelButton:true
        })
        .then(async(res)=>{
          try {
            if(res.isConfirmed){
              const response = await client.delete(`/jobs/${id}`);
              responseHandler(response.data);
              window.location.reload();
            }
          } catch (error) {
            const err= {error:error.message}
            responseHandler(err);
          }
        })
        
      
    }

    if(JobData.length<=0){
        return <div className='d-flex justify-content-center align-items-center'>
            <img src={Loading} alt="Loading..." width={70} height={70} />
            </div>;
    }
    if (!Array.isArray(JobData)) {
        return <div className='h4 text-center'>No data available.</div>;
    }
  return (
    <div className="view-user-container">
      <div className="page-title-box d-flex justify-content-between">
        <h4 className="page-title">View Jobs</h4>
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
            <li className="breadcrumb-item">Jobs</li>
            <li className="breadcrumb-item active" aria-current="page">View Job</li>
          </ol>
        </nav>
      </div>
      <div className="content">
        <form action="" className="row">
          <div className="col-2">
            <div className="d-flex align-items-baseline">
              <label htmlFor="" className="col">Show Entries:</label>
              <select
                name=""
                id=""
                className="form-select en-numbers"
                onChange={handleSizeChange}
              >
                <option value="10">10</option>
                <option value="25">25</option>
                <option value="50">50</option>
                <option value="100">100</option>
              </select>
            </div>
          </div>
        </form>
        <div className="col mt-4">
          <table className="table table-hover">
            <thead>
              <tr>
                <th>#</th>
                {tableHeaders.map((item, index) => (
                  <th key={index}>{item}</th>
                ))}
                <th>ACTION</th>
                
              </tr>
            </thead>
            <tbody>
              {records.map((item, index) => (
                <tr key={index}>
                  <td>{index+1}</td>
                  <td><Link to={`/view-job/${item.id}`}>{item.job_no}</Link></td>
                  <td>{item.category}</td>
                  <td>{item.deadline}</td>
                  <td>{item.location}</td>
                  <td>{item.comments}</td>
                  <td>{item.status}</td>
                  <td>
                    <Link to={`/edit-job/${item.id}`}>
                      <button className="btn edit"><i className="bi bi-pencil-square"></i></button>
                    </Link>
                    
                    <button className="btn text-danger" onClick={()=>{del(item.id)}}><i className="bi bi-trash"></i></button>
                  </td>
                  
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        <Pagination
          nPage={nPage}
          pageNumbers={numbers}
          currentPage={currentPage}
          setCurrentPage={setCurrentPage}
          totalEntries={JobData.length}
          itemsPerPage={recordsPerPage}
        />
      </div>
    </div>
  )
}

export default ViewJobs
