import React,{useState,useEffect} from 'react';
import './job.css';
import Select from 'react-select';
import RadioInput from '../../components/formComponents/RadioInput';
import { useForm, Controller } from 'react-hook-form';
import { client } from '../../Constant';
import Swal from 'sweetalert2';
import { responseHandler } from '../../components/api/logics';
import { Link } from 'react-router-dom';


const AddJob = () => {
  const { register, handleSubmit, control } = useForm();
  const [inputFields, setInputFields] = useState([{ id: 1 }]);
  const [multiImage, setMultiImage] = useState([]);
  const [loc, setLoc] = useState(null);
  const [eqp, setEqp] = useState(null);
  const floors = [
    { label: 'TM', value: 'TM' },
    ...Array.from({ length: 27 }, (_, index) => ({
      value: (index + 1).toString(),
      label: (index + 1).toString(),
    })),
  ];
  const addInputField = () => {
    if (inputFields.length < 5) {
      const newInputFields = [...inputFields, { id: inputFields.length + 1 }];
      setInputFields(newInputFields);
    } else {
      alert('You can only add up to 5 input fields.');
    }
  }

  const removeInputField = (idToRemove) => {
    
    const updatedInputFields = inputFields.filter((field) => field.id !== idToRemove);
    setInputFields(updatedInputFields);
  }

  const handleImageUpload =async (e) => {
    const imageSt =  e.target.files[0]
    setMultiImage([...multiImage,imageSt]);
}
    // fetching data from api
    useEffect(()=>{
      const fetchJob = async()=>{
        const locations = await getLoc();
        setLoc(locations);

        const eqp = await getEqp();
        setEqp(eqp);

      }
      fetchJob()
    },[]);
  //get location
  const getLoc =async()=>{
        try {
            const response = await client.get(`/location/get-all`);
            return response.data;
        } catch (error) {
            Swal.fire({
                title:error.message,
                
                icon:'error',
                confirmButtonText:'OK'
            })
            return error.message;
        }
  }
    //get equipment
  const getEqp =async()=>{
        try {
            const response = await client.get(`/equipment/get-all`);
            return response.data;
        } catch (error) {
            Swal.fire({
                title:error.message,
                
                icon:'error',
                confirmButtonText:'OK'
            })
            return error.message;
        }
  }
  let eqpData=[];
  let locSelect =[];
  
  if(Array.isArray(loc)){
    loc.map((loc)=>{
      locSelect.push({
        value:loc.room,
        label:`${loc.room}(${loc.location_type})`
      });
      return null;
    })
  }
  if(Array.isArray(eqp)){
    eqp.map((e)=>{
      eqpData.push({
        value:e.name,
        label:`${e.name}`
      })
      return null;
    });
  }

  // submit form
  const onSubmit=async(data)=>{
      const response = await post(data);
      responseHandler(response);
  }

  //post job
  const post =async(data)=>{
    const currentDate = new Date();

    const year = currentDate.getFullYear();
    const month = currentDate.getMonth() + 1; 
    const day = currentDate.getDate();
    try {

      const formData = new FormData();
      formData.append('insert_by',1);
      formData.append('created_date',`${year}-${month}-${day}`);
      formData.append('critical',data.critical);
      formData.append('floor_no',data.floor.value);
      formData.append('location',data.location.value);
      formData.append('equipment',data.equipment.value);
      formData.append('comments',data.comment);
      formData.append('category','Corrective');
      formData.append('created_by','Pasindu Rashmika');
      multiImage.forEach((image)=>formData.append('picture[]',image));
      const response = await client.post('/jobs/',formData,{
        headers:{
          'Content-Type':'multipart/form-data'
        }
      });
      return response.data;
    } catch (error) {
      return {error:error.message}
    }
  }


  return (
    <div className='add-job-container'>
      <div className="heading d-flex justify-content-between">
        <div className="page-title"><h4>Add Job</h4></div>
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><Link to={'/'}>Home</Link> </li>
            <li className="breadcrumb-item active" aria-current="page">Library</li>
          </ol>
        </nav>
      </div>
      <div className="col bg-white content">
        <form action="" className='col' onSubmit={handleSubmit(onSubmit)}>
          <div className="d-flex justify-content-between">
            {/* Critical */}
            <div className="col">
              <div className="row">
                <label htmlFor="critical" className='col-3 fw-bold'>Critical:</label>
                <div className="col">
                  <div className="d-flex">
                    {['Low', 'Medium', 'High'].map(value => (
                      <RadioInput
                        key={value}
                        label={value}
                        name={'critical'}
                        value={value}
                        register={register}
                      />
                    ))}
                  </div>
                </div>
              </div>
              
            </div>
            
            {/* Floor No */}
            <div className="col">
              <div className="row">
                <label htmlFor="user-type" className='col-3 fw-bold'>Floor No:</label>
                <Controller
                  name="floor"
                  control={control}
                  render={({ field }) => (
                    <Select
                      className='col-6 p-0'
                      {...field}
                      options={floors}
                      isSearchable={true}
                      placeholder="Select floor"
                    />
                  )}
                />
              </div>
            </div>
          </div>
          {/* Location */}
          <div className="d-flex justify-content-between mt-3">
            <div className="col-6">
              <div className="row">
                <label htmlFor="user-type" className='col-3 fw-bold'>Location:</label>
                <Controller
                  name="location"
                  control={control}
                  render={({ field }) => (
                    <Select
                      className='col-6 p-0'
                      {...field}
                      options={locSelect}
                      isSearchable={true}
                      placeholder="Select location"
                    />
                  )}
                />
              </div>
            </div>
            {/* Equipment */}
            <div className="col-6">
              <div className="row">
                <label htmlFor="user-type" className='col-3 fw-bold'>Equipment:</label>
                <Controller
                  name="equipment"
                  control={control}
                  render={({ field }) => (
                    <Select
                      className='col-6 p-0'
                      {...field}
                      options={eqpData}
                      isSearchable={true}
                      placeholder="Select equipment"
                    />
                  )}
                />
              </div>
            </div>
          </div>
          {/* Comment */}
          <div className="d-flex justify-content-between mt-3">
            <div className="col-6">
              <div className="row">
                <label htmlFor="comment" className='col-3 fw-bold'>Comment:</label>
                <textarea className='form-control col-6' id="" cols="10" rows="5" {...register('comment')}></textarea>
              </div>
            </div>
            {/* Photos */}
            <div className="col-6">
              {inputFields.map((field, index) => (
                <div className="row" key={index}>
                  <label htmlFor="images" className='col-3 fw-bold'>Photos:</label>
                  <input className="form-control col-6" type="file" id="formFileMultiple" multiple 
                    onChange={handleImageUpload}
                  />
                  {inputFields.length < 5 && (
                    <button type='button' className="btn text-success col-1 " onClick={addInputField}><i className="bi bi-plus-circle-fill h4"></i></button>
                  )}
                  {field.id !== 1?<button type='button' className='btn text-danger col-1' onClick={()=>{removeInputField(field.id)}}><i className="bi bi-x-circle-fill h4"></i></button>:''}
                  
                </div>
              ))}
              
            </div>
          </div>
          {/* Additional Fields... */}
          <div className="d-flex justify-content-between mt-3">
            {/* Add more fields as needed */}
          </div>
          <div className="d-flex justify-content-between mt-3">
            <div className="col-12">
              <button type="submit" className='btn btn-primary'>Submit</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default AddJob;



