import React,{useRef, useState,useEffect} from 'react';
import { CSSTransition } from 'react-transition-group';
import './sidebar.css';
import Logo from '../../assets/images/side_bar_logo.png';
import MenuItem from './MenuItem';


const SideBar = ({ isOpen }) => {
    const [openMenu, setOpenMenu] = useState(null);
    const menuRef = useRef(null);
    
    const items = [
        {
            title:"Dashboard",
            icon:'bi bi-grid',
            menuItem:[
                {link:'/', text:'Dashboard', icon:"bi bi-house-door"}
            ]
        },
        {
            title:"Job",
            icon:'bi bi-card-checklist',
            menuItem:[
                {link:'/add-job', text:'Add Job'},
                {link:'/view-jobs', text:'View Jobs'}
            ]
        },
        {
            title:"user",
            icon:'bi bi-people',
            menuItem:[
                {link:'/add-user', text:'Add User'},
                {link:'/view-users', text:'View Users'},
                {link:'/archived-users',text:'Archived Users'}
            ]
        },
        {
            title:"Department",
            icon:"bi bi-border-all",
            menuItem:[
                {link:'/add-department', text:'Add Department'},
                {link:'/view-department', text:'View Departments'}
            ]
        },
        {
            title:"Location",
            icon:"bi bi-map",
            menuItem:[
                {link:'/add-location', text:'Add Location'},
                {link:'/view-locations', text:'View Locations'}
            ]
        },
        {
            title:"equipment categories",
            icon:"bi bi-bookmarks",
            menuItem:[
                {link:'/add-eqp-cat', text:'Add New Category'},
                {link:'/view-eqp-cat', text:'View All Categories'}
            ]
        },
        {
            title:"Equipment Manager",
            icon:'bi bi-tools',
            menuItem:[
                {link:'/add-eqp', text:'Add Equipment'},
                {link:'/view-eqp', text:'View All Equipment'}
            ]
        },
        {
            title:"Meter Readings",
            icon:'bi bi-speedometer',
            menuItem:[
                {link:'/add-meter', text:'Add Meter'},
                {link:'/view-meter', text:'View Meter'},
                {link:'/meter-reports', text:'Meter Reading Reports'}
            ]
        },
        {
            title:"Reports",
            icon:'bi bi-bar-chart-line',
            menuItem:[
                {link:'/reports', text:'Reports'}
            ]
        },
        {
            title:"Settings",
            icon:"i bi-gear",
            menuItem:[
                {link:'/settings', text:'Settings'}
            ]
        },
        
    ];
    const handleTitleClick = (index) => {
        
        setOpenMenu(openMenu === index ? null : index);
        

    };
    const handleClickOutside = (event) => {
        if (menuRef.current && !menuRef.current.contains(event.target)) {
            setOpenMenu(null);
        }
    };
    useEffect(() => {
        document.addEventListener('mousedown', handleClickOutside);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, []);
    
  return (
    <div className={`side-menu open `}>
        
        <div  className="item-container d-flex justify-content-between">
        
            {items.map((item, index) => (
                <div className="item row" key={index}>
                    <div className='d-flex justify-content-between'>
                        
                        <div 
                        className='title' 
                        style={openMenu === index ? { color: '#5369f8' } : {}} 
                        onClick={() => handleTitleClick(index)}
                        ><i className={item.icon}></i>{item.title}</div>
                        <div>
                            {openMenu === index ? (
                                    <i className="bi bi-chevron-down"></i>
                                ) : (
                                    <i className="bi bi-chevron-right"></i>
                                )
                            }
                        </div>
                    </div>
                    
                    {openMenu === index && (
                        <CSSTransition
                            key={index}
                            in={openMenu === index}
                            timeout={1000}
                            classNames="menu-class"
                            unmountOnExit
                        >
                            <div className='menuItem ' ref={menuRef}>
                            {item.menuItem.map((menu, innerIndex) => (
                                <MenuItem key={innerIndex} link={menu.link} text={menu.text} />
                            ))}
                            </div>
                        </CSSTransition>
                    )}

                </div>

            ))}
        </div>
        
    
        
    </div>
  )
}

export default SideBar
