import React, { useEffect, useState } from 'react';
import './user.css';
import { Link, useParams } from 'react-router-dom';
import PersonalDetails from './personalDetails/PersonalDetails';
import JobList from '../../components/tableComponent/jobList/JobList';
import { client } from '../../Constant';
import Swal from 'sweetalert2';


const ViewUser = () => {
    const {id} = useParams();
    const userId = +id;
    const [userProfile,setUserProfile]=useState([]);
    
    
    useEffect(()=>{
        const fetchData = async () => {
        try {
            const response = await getUser(userId);
            setUserProfile(response)
            //console.log(response);

        } catch (error) {
            console.log(error);
        }
    };

    fetchData();
    },[]);
    const getUser = async(id)=>{
        try {
            const response = await client.get(`/users/${id}`);
            return response.data;
        } catch (error) {
            console.log(error);
            Swal.fire({
                title:error.message,
                icon:'error',
                confirmButtonText:'OK'
            })
            return error.message;
        }
        
    }
    
    
  return (
    <div className='view-user-container'>
        <div className="page-title-box d-flex justify-content-between">
            <h4 className="page-title">Profile</h4>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb">
                    <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
                    <li className="breadcrumb-item">User</li>
                    <li className="breadcrumb-item" ><Link to={'/view-users'}>View Users</Link></li>
                    <li className="breadcrumb-item active" aria-current="page">View User</li>
                </ol>
            </nav>
        </div>
        <div className="row">
            <div className="col-3">
                <PersonalDetails userProfile={userProfile} />
            </div>
            <div className="col-9">
                
                <JobList tableData={[]}/>
            </div>
            
        </div>
    </div>
  )
}

export default ViewUser
