import React,{useState} from 'react';
import { Link } from 'react-router-dom';
import { useForm ,Controller} from 'react-hook-form';
import Select from 'react-select';
import TextInput from '../../components/formComponents/TextInput';
import { client } from '../../Constant';
import { responseHandler } from '../../components/api/logics';
import Loading from '../../assets/images/loading.gif';
import { useDropzone } from 'react-dropzone';
import Swal from 'sweetalert2';


const AddLocation = () => {
  const {register, handleSubmit,control} =useForm();
  const [loading,setLoading] = useState(false);
  const [uploadedFiles, setUploadedFiles] = useState([]);
  const [openUpload, setOpenUpload] = useState(false);

  //drop csv
  const onDrop = (acceptedFiles, rejectedFiles) => {
    if (rejectedFiles && rejectedFiles.length > 0) {
      const invalidFile = rejectedFiles[0];
      const invalidFileType = invalidFile.file.type || 'Unknown';
      
      Swal.fire({
        title:'Invalid file type',
        text:`You are selected ${invalidFileType} file. Please select CSV file!`,
        icon:'error',
        confirmButtonText:'Ok'

      })
      
    } else {
      setUploadedFiles(acceptedFiles);
    }
  };

  const { getRootProps, getInputProps } = useDropzone({
    onDrop,
    accept: {'text/csv':[]}, // Specify the accepted file types
  });

  // set file 
  const setUpload = async()=>{
    try{
      setLoading(true)
      const response = await upload();
      responseHandler(response);
    }finally{
      setLoading(false);
    }
    

  }

  //upload
  const upload =async()=>{
    try {
      
      const formData = new FormData();
      formData.append('temp',uploadedFiles[0]);
      const response = await client.post('/location/bulk-upload',formData,{
        headers:{
          'Content-Type':'multipart/form-data'
        }
      });
      
      return response.data;
    } catch (error) {
      
      return {error:error.message}
    } 
  }

  // loc types
  const locType = [
    {
      value:"Common areas",
      label:"Common areas"
    },
    {
      value:"Rooms",
      label:"Rooms"
    }
  ];

  // handle submit
  const onSubmit=async(data)=>{
    try{
      setLoading(true);
      const response = await post(data);
      responseHandler(response);
    }finally{
      setLoading(false);
    }
    
  }

  //post req
  const post =async(data)=>{
    try {
      const response = await client.post('/location/',{
        location_type:data.loc_type.value,
        room:data.room,
        floor_no:data.floor_no,
        description:data.desc
      });
      return response.data;
    } catch (error) {
      return {error:error.message}
    }
  }
  
  
  
  return (
    <div>
      <div className="page-title-box d-flex justify-content-between">
        
          <h4 className="page-title ">Add Location</h4>
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
            <li className="breadcrumb-item"><Link to={'/department'}>Locations</Link></li>
            <li className="breadcrumb-item active" aria-current="page">Add Location</li>
          </ol>
        </nav>
      </div>
      <div className="content">
        <div className="col">
            <form action="" className='col' onSubmit={handleSubmit(onSubmit)}>
            <div className="d-flex justify-content-end">
              <button type='button' className='btn btn-success' onClick={()=>setOpenUpload(true)}><i className="bi bi-upload" ></i> Bulk Upload</button>
            </div>
              <div className="row">
                <div className="col-6">
                  <div className="row mt-3">
                    <label htmlFor="loc_type" className='col-3 fw-bold'>Location Type:</label>
                    <Controller
                      name="loc_type"
                      control={control}
                      render={({ field }) => (
                        <Select
                          className='col-6 p-0'
                          {...field}
                          options={locType}
                          isSearchable={true}
                          placeholder="Select User Type"
                        />
                      )}
                    />
                  </div>
                  
                  
                </div>
                <TextInput 
                    label={"Room name or no"}
                    type={"text"}
                    name={"room"}
                    register={register}
                  />
              </div>
              <div className="row mt-3">
                <div className="col-6">
                  <div className="row">
                    <label htmlFor="floor_no" className='col-3 fw-bold'>Floor No:</label>
                    <input type="number" className='form-control col-6' {...register('floor_no')} />
                  </div>
                </div>
                <div className="col-6">
                  <div className="row">
                    <label htmlFor="description" className='col-3 fw-bold'>Description:</label>
                    <textarea name="" id="" cols="10" rows="3" className='form-control col-6' {...register('desc')}></textarea>
                  </div>
                  
                </div>
              </div>
              <div className="col">
                <input type="submit" className='btn btn-primary' value=" Submit" />
              </div>
            </form>
            
        </div>
      </div>
      {loading && (
        <div className='waiting-model'>
            <img className='' src={Loading} width={50} height={50} alt="" />
        </div>
      )}
      {openUpload && (
        <div className="upload-container  ">
            <div className="close " onClick={()=>setOpenUpload(false)}>
                  <i className="bi bi-x-circle h3 "></i>
            </div>
          <div {...getRootProps()} className="dropzone text-center bg-white drop-model">
            
          
            <input {...getInputProps()} />
            <p className="mb-2">
              <i className="bi bi-cloud-arrow-up-fill h3"></i>
            </p> 
            <p> Drag and drop files here, or click to select files</p>
          </div>

          {uploadedFiles.length > 0 && (
            <div className="mt-3">
              <h5>Uploaded Files:</h5>
              <ul>
                {uploadedFiles.map((file) => (
                  <li key={file.name}>{file.name}</li>
                ))}
              </ul>
            </div>
          )}

          <div className="row mt-3">
            <button  className="btn btn-primary col" onClick={() => setUploadedFiles([])}>
              Clear
            </button>
            <button type='button' className='btn btn-success col ms-3 ' onClick={setUpload}>Upload</button>
          </div>
          
    </div>
      )}
    </div>
    
  )
}

export default AddLocation
