import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import { client } from '../../Constant';

import { responseHandler } from '../../components/api/logics';
import Swal from 'sweetalert2';
import Select from 'react-select';

const ViewJob = () => {
    const [jobData,setJobData]=useState([]);
    const [tech, setTech]=useState([]);
    const [inputFields, setInputFields] = useState([{ id: 1 }]);
    const [multiImage, setMultiImage] = useState([]);
    const [techData,setTechData] = useState({});
    const [deadline,setDeadline] = useState('');
    const [status,setStatus] =useState('');
    const [cost,setCost] = useState('');
    const [comments,setComments] = useState(''); 
    const{id}=useParams();
    const jobId = +id;
    let job;
    const technician = [];
    
    //fetching data from api
    useEffect(()=>{
        const fetchData = async()=>{
            const response = await getJob(jobId);
            setJobData(response);

            const response2 = await getUser();
            setTech(response2);
        }
        fetchData();
    },[jobId])
    if(jobData.length>0){
        job=jobData[0];
    }
    //get job details
    const getJob = async(id)=>{
    try {
        const response = await client.get(`/jobs/${id}`);
        return response.data;
    } catch (error) {
        //console.log(error);
        Swal.fire({
            icon: "error",
            title: "Oops...",
            text: error.message,
            
        });
        return error.message;
    }
    
    }

    //get Tech details
    const getUser = async()=>{
    try {
        const response = await client.get(`/users/get-tech`);
        return response.data;
    } catch (error) {
        //console.log(error);
        Swal.fire({
            icon: "error",
            title: "Oops...",
            text: error.message,
            
        });
        return error.message;
    }
    
    }
    //set Tech
    if(tech){
            tech.map((item)=>{
            technician.push({
                label:item.name,
                value:item.name
            });
            return null;
        });
    }
    useEffect(()=>{
        const selectedTech = tech.find((t)=>t.name === job.asign_to);
        
        setTechData({
            value:selectedTech?selectedTech?.name:'',
            label:selectedTech?selectedTech?.name:'',
        });
        if(job){
            setDeadline(job.deadline);
        }
        
        
    },[tech,job])
    //job status
    const jobStatus = [
        {value:'Complete',label:'Completed'},
        {value:"Hold",label:'Hold'},
        {value:"Canceled",label:'Canceled'},
    ]
    //handle multiple images
    const addInputField = () => {
        if (inputFields.length < 5) {
          const newInputFields = [...inputFields, { id: inputFields.length + 1 }];
          setInputFields(newInputFields);
        } else {
          alert('You can only add up to 5 input fields.');
        }
      }
    
      const removeInputField = (idToRemove) => {
        
        const updatedInputFields = inputFields.filter((field) => field.id !== idToRemove);
        setInputFields(updatedInputFields);
      }
    
      const handleImageUpload =async (e) => {
        const imageSt = e.target.files[0]
        setMultiImage([...multiImage,imageSt]);
    }
    
    /// update submit 
    const onSubmit = async()=>{
        const response = await update(jobId);
        responseHandler(response);
    }
    //update
    const update = async(id)=>{
        try {
            const formData = new FormData();
            formData.append('deadline',deadline);
            formData.append('asign_to',techData.value);
            formData.append('update_comments',comments);
            formData.append('update_cost',cost);
            formData.append('status',status.value);

            multiImage.map((image)=>formData.append('picture[]',image));

            const response = await client.put(`/jobs/edit/${id}`,formData,{
                headers:{
                    'Content-Type':'multipart/form-data'
                }
            });
            return response.data;

        } catch (error) {
            return {error:error.message}
        }
    }
  
  return (
    <div>
      <div className="page-title-box d-flex justify-content-between">
        <h4 className="page-title">View Job</h4>
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
            <li className="breadcrumb-item"><Link to={'/jobs'}>Jobs </Link></li>
            <li className="breadcrumb-item active" aria-current="page">View Job</li>
          </ol>
        </nav>
      </div>
      <div className="col content">
        <div className="d-flex justify-content-between pb-5 border-bottom">
            <div className="">
                <div className="row mt-2 align-items-baseline">
                    <div className="fw-bold col h6">Status: </div>
                    {job&&<div className={`col fw-medium badge ${job.status==='Complete'?'bg-success':'bg-warning'}`}>{job.status}</div>}
                </div>
                <div className="row mt-2">
                    <div className="fw-bold col h6">Critical: </div>
                    {job&& <div className='col fw-medium'>{job.critical}</div>}
                </div>
                <div className="row mt-2">
                    <div className="fw-bold col h6">Floor no: </div>
                    {job&&<div className='col fw-medium'>{job.floor_no}</div>}
                </div>
                <div className="row mt-2">
                    <div className="fw-bold col h6">Location: </div>
                    {job&&<div className='col fw-medium'>{job.location}</div>}
                </div>
                <div className="row mt-2">
                    <div className="fw-bold col h6">Item/Equipment: </div>
                    {job&&<div className='col fw-medium'>{job.equipment}</div>}
                </div>
                <div className="row mt-2">
                    <div className="fw-bold col h6">Comment: </div>
                    {job&& <div className='col fw-medium'>{job.comments}</div>}
                </div>
            </div>
            <div className="">
                <div className="row mt-2">
                    <div className="fw-bold col h6">Created By: </div>
                    {job && <div className='col fw-medium'>{job.created_by}</div>}
                </div>
                <div className="row mt-2">
                    <div className="fw-bold col h6">Created Date: </div>
                    {job && <div className='col fw-medium'>{job.created_date}</div>}
                </div>
                <div className="d-flex align-items-baseline mt-2">
                    <div className="fw-bold col h6">Assign To: </div>
                    <div>
                    <Select
                      className='col p-0 ms-1'
                      options={technician}
                      isSearchable={true}
                      placeholder="Assign Technician"
                      value={techData}
                      onChange={(selectedValue)=>{setTechData(selectedValue)}}
                    />
                    </div>
                </div>
                <div className="d-flex align-items-baseline mt-2">
                    <div className="fw-bold col h6">Deadline: </div>
                    <div className='col ms-1'>
                    <input type="date" className='form-control' value={deadline} onChange={(e)=>{setDeadline(e.target.value)}} />
                    </div>
                </div>
                <div className="row mt-2">
                    <div className="fw-bold h6">Completed date: </div>
                    {job && <div className='col fw-medium'>{job.completed_date}</div>}
                </div>
            </div>
        </div>
        <div className="col">
            <h4>Update</h4>
            <div className="col-8">
                <div className="row mt-5">
                    <label htmlFor="status" className='col-2 fw-bold'>Job Status:</label>
                    <Select
                        className='col p-0 ms-1'
                        options={jobStatus}
                        isSearchable={true}
                        placeholder="Select Status"
                        onChange={(selectedValue)=>{setStatus(selectedValue)}}
                    />
                </div>
                <div className="d-flex mt-3">
                    <label htmlFor="status " className='col-2 fw-bold'>Update Comments:</label>
                    <textarea className='col form-control' cols="10" rows="5" onChange={(e)=>{setComments(e.target.value)}}></textarea>
                </div>
                <div className="d-flex mt-3">
                    <label htmlFor="status " className='col-2 fw-bold'>Update any cost involved:</label>
                    <textarea className='col form-control' cols="10" rows="5" onChange={(e)=>{setCost(e.target.value)}}></textarea>
                </div>
                
                {inputFields.map((field, index) => (
                        <div className="row mt-3" key={index}>
                            <label htmlFor="images" className='col-2 fw-bold'>Photos:</label>
                            <input className="form-control col ms-1" type="file" id="formFileMultiple" multiple 
                                onChange={handleImageUpload}
                            />
                            {inputFields.length < 5 && (
                                <button className="btn text-success col-1  p-0 m-0" onClick={addInputField}><i className="bi bi-plus-circle-fill h4"></i></button>
                            )}
                            {field.id !== 1?<button className='btn text-danger col-1' onClick={()=>{removeInputField(field.id)}}><i className="bi bi-x-circle-fill h4"></i></button>:''}
                            
                        </div>
                ))}
                
                <button type="submit" onClick={onSubmit} className='btn btn-primary ms-auto mt-3 d-block'>Update</button>
                
            </div>
        </div>
      </div>
    </div>
  )
}

export default ViewJob
