import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import { client } from '../../Constant';
import Swal from 'sweetalert2';
import Loading from '../../assets/images/loading.gif';
import JobList from '../../components/tableComponent/jobList/JobList';


const ViewLocation = () => {
    const {id,room} = useParams();
    const locId = +id;
    const [location, setLocation] = useState([{
        room: '',
        location_type:'',
        floor_no:'',
        description:''
    }]);
    const [waiting,setWaiting] = useState(false);
    const [jobData,setJobData] = useState([]);

    //fetch location data
    useEffect(()=>{
        const fetch = async(id)=>{
            try{
                setWaiting(true);
                const response = await getLoc(id);
                setLocation(response);

                const jobs = await getJobs(room);
                setJobData(jobs);
            }finally{setWaiting(false);}
            
        }
        fetch(locId);
    },[locId])
    //get location
    const getLoc = async(id)=>{
        try {
            const response = await client.get(`/location/${id}`);
            return response.data;
        } catch (error) {
            setWaiting(true)
            Swal.fire({
                icon: "error",
                title: "Oops...",
                text: error.message,
                
            });
            
        }
    }
    // get job data
    const getJobs = async (room)=>{
        try {
            const response = await client.get(`/location/get-room/${room}`);
            return response.data;
        } catch (error) {
            setWaiting(true)
            Swal.fire({
                icon: "error",
                title: "Oops...",
                text: error.message,
                
            });
        }
    }
    
    if(location ==null){
        return (
            <div className="text-center">
                <h4>Data not available!</h4>
            </div>
        )
    }
    
  return (
    <div>
        <div className="page-title-box d-flex justify-content-between">
            <h4 className="page-title">Profile</h4>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb">
                    <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
                    <li className="breadcrumb-item">Locations</li>
                    <li className="breadcrumb-item" ><Link to={'/view-users'}>View Locations</Link></li>
                    <li className="breadcrumb-item active" aria-current="page">View Location</li>
                </ol>
            </nav>
        </div>
        <div className="col">
            <div className="row">
                <div className="col-3 ">
                    <div className="card a-border">
                        <div className="card-body">
                            <h4>Location Details</h4>
                            <div className="table-responsive">
                                {waiting && (
                                    <div className='d-flex align-items-center justify-content-center'>
                                        <img src={Loading} width={70} height={70} alt="loading" />
                                    </div>
                                )}
                                {!waiting &&(
                                        <table className="table table-borderless mb-0 text-muted">
                                    <tbody className='user-data'>
                                        <tr>
                                            <th scope="row">Location:</th>
                                            <td>{location[0].room}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row">Location Type:</th>
                                            <td>{location[0].location_type}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row">Floor No:</th>
                                            <td>{location[0].floor_no}</td>
                                        </tr>
                                        <tr>
                                            <th scope="row">Description:</th>
                                            <td>{location[0].description}</td>
                                        </tr>
                                        
                                    </tbody>
                                </table>
                                    )
                                }
                                
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-9 ">
                    <JobList tableData={jobData||[]} />
                </div>
            </div>
        </div>
    </div>
  )
}

export default ViewLocation
