import React from 'react'
import { useForm ,Controller} from 'react-hook-form';
import Input from '../../components/formComponents/TextInput';
import Select from 'react-select';
import { client } from '../../Constant';
import Swal from 'sweetalert2';
import { responseHandler } from '../../components/api/logics';



const AddUser = () => {
    const {register, handleSubmit,control} =useForm();
    const onSubmit =async (data)=>{
        const response = await createUser(data);
        responseHandler(response);
        console.log("Response:",response.message);
    }

    const userType = [
        {value:'SuperAdmin',label:"Super Admin"},
        {value:'Admin',label:"Admin"},
        {value:'Engineer',label:"Engineer"},
        {value:'Technician',label:"Technician"},
        {value:'Painter',label:"Painter"},
        {value:'User',label:"User"},
    ];

    //send post request
    const createUser = async (data) => {
        try {
            const response = await client.post('/users/create-user', {
            email: data.email,
            userType: data.user_type.value,
            department: data.department.value,
            job_add:data.job_add,
            job_edit:data.job_edit,
            job_view:data.job_view,
            dep_add:data.dep_add,
            dep_edit:data.dep_edit,
            dep_view:data.dep_view,
            loc_add:data.loc_add,
            loc_edit:data.loc_edit,
            loc_view:data.loc_view,
            rost_add:data.rost_add, 
            rost_edit:data.rost_edit, 
            rost_view:data.rost_view, 
            meter_add:data.meter_add,
            meter_edit:data.meter_edit,
            meter_view:data.meter_view, 
            setting:data.setting, 
            cat_add:data.cat_add, 
            cat_edit:data.cat_edit, 
            cat_view:data.cat_view, 
            eqp_add:data.eqp_add, 
            eqp_edit:data.eqp_edit,
            eqp_view:data.eqp_view, 
            super_up:data.super_up, 
            admin_up:data.admin_up, 
            tech_up:data.tech_up, 
            user_up:data.user_up, 
            user_view:data.user_view,
            pending_job:data.pending_job, 
            complete_job:data.complete_job, 
            cancel_job:data.cancel_job, 
            total_job:data.total_job,
            jobcomplete_technician:data.jobcomplete_technician, 
            floorwise_job:data.floorwise_job, 
            departwise_job:data.departwise_job,
            holdpm_work:data.holdpm_work, 
            completepm_work:data.completepm_work,
            upcomingpm_work:data.upcomingpm_work
            });
            return response.data; // Return only the data property
        } catch (error) {
            // Swal.fire({
            //     title:error.message,
            //     icon:'error',
            //     confirmButtonText:'OK'
            // })
            return {error: error.message};
            
            
        }
    };

    

    
    
  return (
    <div className='add-user-container'>
        <div className="page-title-box d-flex justify-content-between">
            <h4 className="page-title">Add User</h4>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb">
                    <li className="breadcrumb-item"><a href="/">Home</a></li>
                    <li className="breadcrumb-item"><a href="#">User</a></li>
                    <li className="breadcrumb-item active" aria-current="page">Add User</li>
                </ol>
            </nav>
        </div>
        <div className="content">
            <form onSubmit={handleSubmit(onSubmit)}>
                <div className="row">
                    <Input 
                    label={'Email Address:'}
                    type={"email"}
                    name={"email"}
                    register={register}
                    
                    />
                    
                    <div className="col-6">
                        <div className="row">
                            <label htmlFor="user-type" className='col-3 fw-bold'>User Type:</label>
                            <Controller
                            name="user_type"
                            control={control}
                            render={({ field }) => (
                                <Select
                                className='col-6 p-0'
                                {...field}
                                options={userType}
                                isSearchable={true}
                                placeholder="Select User Type"
                                />
                            )}
                            />
                                
                        </div>
                    </div>
                </div>
                <div className="row mt-3">
                <div className="col-6">
                        <div className="row">
                            <label htmlFor="department" className='col-3 fw-bold'>Department:</label>
                            <Controller
                            name="department"
                            control={control}
                            render={({ field }) => (
                                <Select
                                className='col-6'
                                {...field}
                                options={userType}
                                isSearchable={true}
                                
                                />
                            )}
                            />
                        </div>
                    </div>
                </div>
                <div className="col-md-12 mt-4"> 
                <div className="row">
                    <div className="col-md-12"><h5 className="permission_u">Permissions</h5></div>									
                    <div className="col-md-3">
                        <h6>Manage jobs</h6>
                        <input type="checkbox" name="job_add" className="pri_cbox" value="1" checked {...register("job_add")}/> <label>Add<span></span></label> <br />
                        <label><input type="checkbox" name="job_edit" className="pri_cbox" value="1"{...register("job_edit")}  /> Edit<span></span></label> <br />
                        <label><input type="checkbox" name="job_view" className="pri_cbox" value="1" checked {...register("job_view")}/> View<span></span></label> <br />
                    </div>
                    <div className="col-md-3">
                        <h6>Departments</h6>
                        <label><input type="checkbox" name="dep_add" className="pri_cbox" value="1" {...register("dep_add")}/> Add<span></span></label> <br />
                        <label><input type="checkbox" name="dep_edit" className="pri_cbox" value="1" {...register("dep_edit")}/> Edit<span></span></label> <br />
                        <label><input type="checkbox" name="dep_view" className="pri_cbox" value="1" {...register("dep_view")}/> View<span></span></label> <br />
                    </div>
                    <div className="col-md-3">
                        <h6>Location manager</h6>
                        <label><input type="checkbox" name="loc_add" className="pri_cbox" value="1" {...register("loc_add")}/> Add<span></span></label> <br />
                        <label><input type="checkbox" name="loc_edit" className="pri_cbox" value="1" {...register("loc_edit")}/> Edit<span></span></label> <br />
                        <label><input type="checkbox" name="loc_view" className="pri_cbox" value="1" {...register("loc_view")}/> View<span></span></label> <br />
                    </div>
                    <div className="col-md-3">
                        <h6>Monthly Roater</h6>
                        <label><input type="checkbox" name="rost_add" className="pri_cbox" value="1" {...register("rost_add")}/> Add<span></span></label> <br />
                        <label><input type="checkbox" name="rost_edit" className="pri_cbox"  value="1" {...register("rost_edit")}/> Edit<span></span></label> <br />
                        <label><input type="checkbox" name="rost_view" className="pri_cbox" value="1" {...register("rost_view")}/> View<span></span></label> <br />
                    </div>
                    <div className="col-md-3">										
                        <h6>Equipments</h6>
                        <label><input type="checkbox" name="cat_add" className="pri_cbox" value="1" {...register("cat_add")}/> Add category<span></span></label> <br />
                        <label><input type="checkbox" name="cat_edit" className="pri_cbox" value="1" {...register("cat_edit")}/> Edit Category<span></span></label> <br />
                        <label><input type="checkbox" name="cat_view" className="pri_cbox" value="1" {...register("cat_view")}/> View Category<span></span></label> <br />
                        <label><input type="checkbox" name="eqp_add" className="pri_cbox" value="1" {...register("eqp_add")}/> Add Equipments<span></span></label> <br />
                        <label><input type="checkbox" name="eqp_edit" className="pri_cbox" value="1" {...register("eqp_edit")}/> Edit Equipments<span></span></label> <br />
                        <label><input type="checkbox" name="eqp_view" className="pri_cbox" value="1" {...register("eqp_view")}/> View Equipments<span></span></label> <br />
                    </div>
                    <div className="col-md-3">
                        <h6>User management</h6>
                        <label><input type="checkbox" name="super_up" className="pri_cbox" value="1" {...register("super_up")}/> Super Admin user Creation/Edit<span></span></label> <br />
                        <label><input type="checkbox" name="admin_up" className="pri_cbox" value="1" {...register("admin_up")}/> Admin user creation /Edit<span></span></label> <br />
                        <label><input type="checkbox" name="tech_up" className="pri_cbox" value="1" {...register("tech_up")}/> Technician Creation/edit<span></span></label> <br />
                        <label><input type="checkbox" name="user_up" className="pri_cbox" value="1" {...register("user_up")}/> Other Users creation/edit<span></span></label> <br />
                        <label><input type="checkbox" name="user_view" className="pri_cbox" value="1" {...register("user_view")}/> View Users<span></span></label> <br />
                    </div>
                    <div className="col-md-3">
                        <h6>Meter Readings</h6>
                        <label><input type="checkbox" name="meter_add" className="pri_cbox" value="1" {...register("meter_add")}/> Add<span></span></label> <br />
                        <label><input type="checkbox" name="meter_edit" className="pri_cbox" value="1" {...register("meter_edit")}/> Edit<span></span></label> <br />
                        <label><input type="checkbox" name="meter_view" className="pri_cbox" value="1" {...register("meter_view")}/> View<span></span></label> <br />
                    </div>
                    
                    
                    <div className="col-md-3">										
                        <h6>Reports</h6>
                        <label><input type="checkbox" name="pending_job" className="pri_cbox" value="1"{...register("pending_job")} /> Pending jobs<span></span></label> <br />
                        <label><input type="checkbox" name="complete_job" className="pri_cbox" value="1" {...register("complete_job")}/> Completed jobs<span></span></label> <br />
                        <label><input type="checkbox" name="cancel_job" className="pri_cbox" value="1" {...register("cancel_job")}/> Canceled jobs<span></span></label> <br />
                        <label><input type="checkbox" name="total_job" className="pri_cbox" value="1" {...register("total_job")}/> Total received jobs<span></span></label> <br />
                        <label><input type="checkbox" name="jobcomplete_technician" className="pri_cbox" value="1" {...register("jobcomplete_technician")}/> Jobs completed by a technician<span></span></label> <br />
                        <label><input type="checkbox" name="floorwise_job" className="pri_cbox" value="1" {...register("floorwise_job")}/> Sort out the jobs floor wise<span></span></label> <br />
                        <label><input type="checkbox" name="departwise_job" className="pri_cbox" value="1" {...register("departwise_job")}/> Sort out the jobs department wise<span></span></label> <br />
                        <label><input type="checkbox" name="holdpm_work" className="pri_cbox" value="1" {...register("holdpm_work")}/> Hold PM works<span></span></label> <br />
                        <label><input type="checkbox" name="completepm_work" className="pri_cbox" value="1" {...register("completepm_work")}/> Completed PM work with Equipments<span></span></label> <br />
                        <label><input type="checkbox" name="upcomingpm_work" className="pri_cbox" value="1" {...register("upcomingpm_work")}/> Upcoming PM work<span></span></label> <br />
                    </div>
                    
                    <div className="col-md-3">
                        <h6>Settings</h6>
                        <label><input type="checkbox" name="setting" className="pri_cbox" value="1" {...register("setting")}/> Update<span></span></label> <br />
                    </div>

                </div>
            </div>
            <div className="mt-5">
                <button type="submit" className='btn btn-primary'>Submit</button>
            </div>
            
            </form>
        </div>
    </div>
  )
}

export default AddUser
