import React from 'react';


const TextInput = ({ label,type, name,register, value,disabled }) => {
    
  return (
    <div className="col mt-3">
      <div className="row">
        <label htmlFor={name} className='col-3 fw-bold'>{label}:</label>
        <input type={type} 
        id={name} 
        className='form-control col-6 ' 
        value={value} 
        disabled={disabled || false}
        {...register(name)} />
      </div>
    </div>
  );
};

export default TextInput;
