import React,{useEffect, useState} from 'react';
import { responseHandler } from '../../components/api/logics';
import { client } from '../../Constant';
import { useParams,Link } from 'react-router-dom';
import TextInput2 from '../../components/formComponents/TextInput2';
import Swal from 'sweetalert2';

const EditDep = () => {
    const [dep,setDep] =useState(null);
    const [name,setName] =useState('');
    
    const {id}= useParams();
    const depId = +id;

    useEffect(()=>{
        const fetchData =async()=>{
            const response = await getDep(depId);
            setDep(response)
        }
        fetchData();
    },[depId]);
    useEffect(()=>{
        if(Array.isArray(dep)){
            setName(dep[0].department_name);
        }
    },[dep])
    
    //get department
    const getDep =async(id)=>{
        try {
            const response = await client.get(`/departments/${id}`);
            return response.data;
        } catch (error) {
            Swal.fire({
            title:error.message,
            
            icon:'error',
            confirmButtonText:'OK'
        })
        }
    }

    if(!Array.isArray(dep)){
        return null;
    }
    
    const handleSubmit =async()=>{
    
        try {
            const response = await client.put(`/departments/update/${depId}`,{department_name:name});
            responseHandler(response.data);
        } catch (error) {
            const err = {error:error.message}
            responseHandler(err)
        }
    
    }
    return (
    <div>
        <div className="page-title-box d-flex justify-content-between">
        <h4 className="page-title">Add Department</h4>
        <nav aria-label="breadcrumb">
            <ol className="breadcrumb">
            <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
            <li className="breadcrumb-item">Department</li>
            <li className="breadcrumb-item"><Link to={'/view-department'}>View Department</Link></li>
            <li className="breadcrumb-item active" aria-current="page">Edit</li>
            </ol>
        </nav>
        </div>
        <div className="content">
        <div   className='d-flex align-items-baseline'>
            <TextInput2
            label={"Department Name"}
            type={"text"}
            name={'dep_name'}
            value={name}
            onchange={(e)=>{setName(e)}}
            />
            <button type="submit" 
            onClick={handleSubmit} 
            className='btn btn-primary  mt-3 ms-3 d-block'
            >Submit</button>
        </div>
        
        </div>
    </div>
    )
}

export default EditDep
