import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import TextInput2 from '../../components/formComponents/TextInput2';
import { client } from '../../Constant';
import { responseHandler } from '../../components/api/logics';

const AddDepartment = () => {
  const [name,setName] =useState(null);

  const handleSubmit =async()=>{
    
    try {
      const response = await client.post('/departments/add-department',{department_name:name});
      responseHandler(response.data);
    } catch (error) {
      const err = {error:error.message}
      responseHandler(err)
    }
    
  }
  return (
    <div>
      <div className="page-title-box d-flex justify-content-between">
        <h4 className="page-title">Add Department</h4>
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><Link to={'/'}>Home</Link></li>
            <li className="breadcrumb-item"><Link to={'/department'}>Department</Link></li>
            <li className="breadcrumb-item active" aria-current="page">Add Department</li>
          </ol>
        </nav>
      </div>
      <div className="content">
        <div   className='d-flex align-items-baseline'>
          <TextInput2
            label={"Department Name"}
            type={"text"}
            name={'dep_name'}
            onchange={(e)=>{setName(e.target.value)}}
          />
          <button type="submit" 
          onClick={handleSubmit} 
          className='btn btn-primary  mt-3 ms-3 d-block'
          >Submit</button>
        </div>
        
      </div>
    </div>
  )
}

export default AddDepartment
