import React, { useEffect, useState } from 'react';
import './user.css';
import Logo from '../../assets/images/logo-image.png';
import TextInput from '../../components/formComponents/TextInput';
import { useForm } from 'react-hook-form';
import { useParams } from 'react-router-dom';
import { client } from '../../Constant';
import Swal from 'sweetalert2';
import { responseHandler } from '../../components/api/logics';



const CreateAccount = () => {
    const {register, handleSubmit} =useForm();
    
    const {id} = useParams();

    const onSubmit =async (data)=>{
        console.log(data)
        if(!isPasswordConfirmed(data)){
            Swal.fire({
                icon:'error',
                title:'Error',
                titleText:'Password missed match!',
                confirmButtonText:'OK'
            })
            return null;
        }
        const response = await createUser(id,data);
        responseHandler(response);
    }
    useEffect(()=>{
        const fetchData = async () => {
        try {
            const response = await getUser(id);
            //console.log(response);

            
            if(!isUser(response)){
                Swal.fire({
                    title: "This user invite is invalid!",
                    icon:'error',
                    confirmButtonText: "OK",
                    }).then((result) => {
                        if (result.isConfirmed) {
                            window.location.href='/';
                        } 
                    });
                return null;
            }
            if(!isLinkValid(response)){
                Swal.fire({
                    title: "This user invite link expired!",
                    icon:'error',
                    confirmButtonText: "OK",
                    }).then((result) => {
                        if (result.isConfirmed) {
                            window.location.href='/';
                        } 
                    });
                return null;
            }
        } catch (error) {
            console.log(error);
        }
    };

    fetchData();
    },[]);
    const getUser = async(id)=>{
        try {
            const response = await client.get(`/users/${id}`);
            return response.data;
        } catch (error) {
            //console.log(error);
            Swal.fire({
                title:error.message,
                icon:'error',
                confirmButtonText:'OK'
            })
            return error.message;
        }
        
    }
    //check user id
    const isUser =(response)=>{
        if(response.length>0){
            return true;
        }
        return false;
    }
    //check account status
    const isLinkValid = (response)=>{
        
        if(response[0].status==='pending'){
            return true;
        }
        return false
    }

    //create user account
    const createUser = async (id,data)=>{

        const formData = new FormData();
        formData.append('uid', id);
        formData.append('name', data.fname);
        formData.append('username', data.username);
        formData.append('sap', data.sap);
        formData.append('contact', data.contact);
        formData.append('password', data.password);
        formData.append('avatar', data.file[0]);
        console.log(formData)
        const response = await client.put('/users/create-account',formData,{
            headers: {
                'Content-Type': 'multipart/form-data',
              },
        });
        return response.data;
    }
    // check password
    const isPasswordConfirmed =(data)=>{
        if(data.password === data.c_password){
            return true;
        }
        
        return false;
    }
  return (
    <div className='container'>
        <div className="col">
            <div className="d-flex align-items-center">
                <div className="col-4">
                    <img src={Logo} alt="cinnamon grand" width={500} />
                </div>
                <div className="col-7 user-form  ">
                    <div className="h3 text-center">Create Account</div>
                    <form onSubmit={handleSubmit(onSubmit)} className='col bg-white p-3 mx-auto'>
                        <TextInput 
                            label={'User ID'}
                            type={'text'}
                            name={'uid'}
                            register={register}
                            value={id}
                            disabled={true}
                        />
                        <TextInput 
                            className='mt-3'
                            label={'Name in Full'}
                            type={'text'}
                            name={'fname'}
                            register={register}
                        />
                        <TextInput 
                            className='mt-3'
                            label={'Username'}
                            type={'text'}
                            name={'username'}
                            register={register}
                        />
                        <TextInput 
                        className='mt-3'
                            label={'SAP No'}
                            type={'text'}
                            name={'sap'}
                            register={register}
                        />
                        <TextInput 
                        className='mt-3'
                            label={'Contact No'}
                            type={'tel'}
                            name={'contact'}
                            register={register}
                        />
                        <TextInput 
                        className='mt-3'
                            label={'Password'}
                            type={'Password'}
                            name={'password'}
                            register={register}
                        />
                        <TextInput 
                        className='mt-3'
                            label={'Confirm Password'}
                            type={'Password'}
                            name={'c_password'}
                            register={register}
                        />
                        <div className="row mt-4">
                            <div className="col">
                                <div className="row">
                                    <label htmlFor="" className='col-3 fw-bold'>Picture:</label>
                                    <input className="form-control col-6" type="file" id="formFileMultiple" multiple 
                                    {...register('file')}
                                    
                                    />
                                </div>
                            </div>
                        </div>
                        <div className="row mt-4 ">
                            <div className="col-3"></div>
                            <button className=' col-6 btn btn-primary '>Create Account</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
      
    </div>
  )
}

export default CreateAccount
